.dealers-application {
  padding: 100px 0;
  background-color: #15222a;;
  color: var(--light-color);
  margin-bottom: 100px;
  @include mobile{
    padding: 50px 0;
    margin-bottom: 50px;
  }
  &__content{
    display: grid;
    grid-template-columns: minmax(200px, 550px) minmax(200px, 470px);
    align-items: center;
    gap: 60px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .dealers-application__title

  &__title {
    margin: 0;
    margin-bottom: 15px;
  }

  // .dealers-application__subtitle

  &__subtitle {
    margin-bottom: 35px;
  }

  // .dealers-application__advantages

  &__advantages {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px 15px;
  }

  // .dealers-application__advantage

  &__advantage {
    border-radius: 20px;
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }

  // .dealers-application__advantage-title

  &__advantage-title {

  }

  // .dealers-application__advantage-img

  &__advantage-img {
  }

  // .dealers-application__form

  &__form {
    border-radius: 10px;
    padding: 30px;
    background-color: var(--light-color);
  }

  // .dealers-application__form-title

  &__form-title {
    margin: 0;
    margin-bottom: 40px;
    color: var(--black-color);
  }

  &__form-inputs{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-bottom: 32px;
  }

  // .dealers-application__form-input

  &__form-input {
    padding-bottom: 14px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);

  }

  // .dealers-application__form-submit

  &__form-submit {
    width: 100%;
    text-align: center;
  }
}

.dealers-advantages {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .dealers-advantages__title

  &__title {
    margin: 0;
