  }

  // .dealers-advantages__wrapper

  &__wrapper {
    background: linear-gradient(180deg, #06828e 0%, #15222a 100%);
    padding: 35px 30px;
    border-radius: 15px;
  }

  // .dealers-advantages__accordion

  &__accordion {
  }

  // .dealers-advantages__accordion-item

  &__accordion-item {
  }

  &__accordion-head{
    @include hover{
      .dealers-advantages__accordion-head-ques{
        color: var(--black-color);
      }
      .dealers-advantages__accordion-head-indicator{
