.dealers-partners {
  .tabs__panel{
    height: 100%;
  }
  // .dealers-partners__title

  &__title {
  }

  &__tabs-wrapper{
    display: grid;
    grid-template-columns: minmax(200px, 450px) minmax(200px, 660px);
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .dealers-partners__tabs-nav

  &__tabs-nav {
    border: 1px solid #ededed;
    border-radius: 15px;
    background-color: var(--light-color);
    padding: 45px 30px;
    position: relative;
    min-width: 100%;
    display: flex;
    align-items: center;
    .tabs__nav-btn{
      font-family: var(--font-family-accent);
      font-weight: 400;
      border: 0;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 8px 16px;
      border-radius: 10px;
      cursor: pointer;
      @include hover{
        background-color: var(--accent-color);
        color: var(--light-color);
        svg{
          path{
            stroke: var(--light-color);
          }
        }
      }
      &--active{
        background-color: var(--accent-color);
        color: var(--light-color);
        svg{
          path{
            stroke: var(--light-color);
          }
        }
      }
    }
  }

  // .dealers-partners__swiper

  &__swiper {
    max-height: 300px;
    width: 100%;
  }

  // .dealers-partners__swiper-slide

  &__swiper-slide {
    display: flex;
  }

  &__swiper-prev, &__swiper-next {
    inset: unset;
    margin: 0;
    padding: 0;
    transform: unset;
    &::after{
      display: none;
    }
  }

  // .dealers-partners__swiper-prev

  &__swiper-prev {
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  // .dealers-partners__swiper-next

  &__swiper-next {
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  // .dealers-partners__content

  &__content {
    display: flex;
    align-items: flex-start;
    gap: 25px;
    padding: 45px 30px;
    background-color: var(--black-color);
    border-radius: 15px;
    color: var(--light-color);

    height: 100%;
    @include mobile{
      flex-direction: column-reverse;
    }
  }

  // .dealers-partners__content-info

  &__content-info {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  // .dealers-partners__content-title

  &__content-title {
    margin: 0;
    margin-bottom: 18px;
    font-weight: 600;
  }

  // .dealers-partners__content-deskr

  &__content-deskr {
    line-height: 147%;
    margin: 0;
    margin-bottom: 50px;
  }

  // .dealers-partners__social

  &__social {
    display: flex;
    align-items: center;
    gap: 40px;
    margin-bottom: 20px;
    margin-top: auto;
  }

  // .dealers-partners__social-item

  &__social-link {
    @include hover{
      opacity: .8;
    }
  }

  // .dealers-partners__links

  &__links {
    display: flex;
    flex-wrap: wrap;
    gap: 20px 45px;
  }

  // .dealers-partners__link

  &__link {
    color: inherit;
    font-weight: 700;
    text-decoration: underline;
    @include hover{
      opacity: .8;
    }
  }

  // .dealers-partners__content-logo

  &__content-logo {
  }
}

