.steps {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 100px;
  @include small-tablet{
    grid-template-columns: repeat(2, 1fr);
  }
  @include mobile{
    margin-bottom: 50px;
    grid-template-columns: 1fr;
    gap: 30px;
  }
  // .steps__item

  &__item {
    padding: 0 40px 15px 40px;
    display: flex;
    flex-direction: column;
    @include mobile{
      padding: 0;
    }
    &:first-child{
      padding-left: 0;
      .steps__item-content{
        padding-left: 40px;
        border-left: 1px solid rgba(0, 0, 0, 0.2);
        @include mobile{
          border: unset;
          padding: 0;
        }
      }
    }
    &:last-child{
      padding-right: 0;
      .steps__item-content{
        padding-right: 40px;
        border-right: 1px solid rgba(0, 0, 0, 0.2);
        @include mobile{
          border: unset;
          padding: 0;
        }
      }
    }
    &:nth-child(2){
      border-left: 1px solid rgba(0, 0, 0, 0.2);
      border-right: 1px solid rgba(0, 0, 0, 0.2);
      @include mobile{
        border: unset;
      }
    }
  }

  &__item-content{

  }

  // .steps__item-num

  &__item-num {
    font-size: 30px;
    color: var(--accent-color);
    margin-bottom: 20px;
  }

  // .steps__item-title

  &__item-title {
    line-height: 120%;
    margin: 0;
    margin-bottom: 15px;
  }

  // .steps__item-text

  &__item-text {
  }
}
