.hero {
  // .hero__swiper

  &__swiper {

  }

  // .hero__slide

  &__slide {
    background-repeat: no-repeat;
    background-position: right;
    @include tablet{
      background-position: unset;
    }
    &--cover{
      background-size: cover;
    }
  }

  // .hero__swiper-item

  &__swiper-item {
    min-height: 550px;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include mobile{
      min-height: calc(100dvh - 77px);
    }
  }

  // .hero__swiper-item--bg

  &__swiper-item--bg {
  }

  // .hero__content

  &__content {
  }

  // .hero__title

  &__title {
    line-height: 116%;
    margin: 0;
    margin-bottom: 12px;

      span{
        background-size: 100% 100%;
        color: var(--light-color);
        display: inline;
        padding: 30px;
        margin-left: -30px;
        @include tablet{
          margin-left: 0;
        }
      }
  }

  // .hero__subtitle

  &__subtitle {
    font-weight: 400;
    font-size: 17px;
    margin-bottom: 25px;
  }

  // .hero__link-application

  &__link-application {
    font-weight: 400;
    font-size: 16px;
    line-height: 137%;
    color: var(--light-color);

    padding: 13px 29px;
    border-radius: 54px;
    background-color: var(--black-color);
    display: inline-block;

    margin-bottom: 50px;

    @include hover{
      background-color: var(--accent-color);
    }
  }

  // .hero__advantages-list

  &__advantages-list {
    display: flex;
    gap: 20px;
  }

  // .hero__advantages-item

  &__advantages-item {
    display: flex;
    align-items: center;
    gap: 10px;

    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: var(--black-color);
  }

  // .hero__pagination

  &__pagination{
    .swiper-pagination-bullet{
      width: 15px;
      height: 15px;
      background-color: #bfbfbf;
    }
    .swiper-pagination-bullet-active{
      background-color: var(--accent-color);
    }
  }
}

.catalog-pdf {
  background-color: var(--darkgreen-color);
  padding: 100px 0;
  // .catalog-pdf__body

  &__body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 35px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .catalog-pdf__content

  &__content {
  }

  // .catalog-pdf__title

  &__title {
    color: var(--light-color);
    margin: 0;
    margin-bottom: 16px;
    line-height: 149%;
  }

  // .catalog-pdf__subtitle

  &__subtitle {
    color: var(--light-color);
    margin-bottom: 25px;
  }

  // .catalog-pdf__advantages-list

  &__advantages-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-bottom: 25px;
  }

  // .catalog-pdf__advantages-item

  &__advantages-item {
    display: flex;
    flex-direction: column;
    gap: 20px;

    padding: 16px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    min-height: 175px;
  }

  // .catalog-pdf__advantages-img

  &__advantages-item-img {
  }

  // .catalog-pdf__advantages-text

  &__advantages-text {
    color: #fff;
  }

  // .catalog-pdf__form

  &__form {
    display: flex;
    align-items: flex-end;
    gap: 15px;
    @include mobile{
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  // .catalog-pdf__input

  &__input {
    background-color: transparent;
    padding-bottom: 5px;
    border-bottom: 1px solid #cdcdcd;
    display: inline;
    padding-left: 5px;

    color: rgba(255, 255, 255, 0.8);
    width: 100%;
    &::placeholder{
      color: rgba(255, 255, 255, 0.4);
    }

    &-name{
      max-width: 113px;
      @include mobile{
        max-width: 100%;
      }
    }

    &-tel{

    }
  }

  // .catalog-pdf__submit

  &__submit {
    border-radius: 121px;
    background-color: var(--light-color);
    padding: 7px 24px;
    text-wrap: nowrap;
    line-height: 125%;
    @include hover{
      background-color: var(--accent-color);
      color: var(--light-color);
        span{
          color: var(--light-color);
        }
    }
    @include mobile{
      grid-column: 2 span;
      text-align: center;
    }

    span{
      font-size: 12px;
      color: #656565;
      text-align: left;
      transition: all .2s ease-in;
      @include mobile{
        text-align: center;
      }
    }
  }

  // .catalog-pdf__img

  &__img {
    background-image: url(../img/catalog-pdf-img.png);
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 25px;
    padding-left: 20px;
    @include tablet{
      background-image: unset;
    }
  }

  // .catalog-pdf__img-text

  &__img-text {
    font-family: var(--font-family-accent);
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 17px;
    color: var(--light-color);
    font-weight: 700;
    line-height: 150%
  }
}

.catalog {
  padding: 100px 0;
  background-color: #ededed;
  @include mobile{
    padding: 50px 0;
  }
  // .catalog__title

  &__title {
    margin-bottom: 50px;
  }

  // .catalog__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 25px 20px;
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .catalog__item

  &__item {
  }

  // .catalog__link

  &__link {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    min-height: 363px;
    background-size: cover;
    @include hover{
      .catalog__link-text{
        background-color: var(--accent-color);
        color: var(--light-color);
      }
    }
  }

  // .catalog__link-text

  &__link-text {
    font-weight: 700;
    font-size: 19px;
    line-height: 126%;
    color: #000;
    padding: 16px;
    background-color: var(--light-color);
    transition: all var(--transition-duration) ease-in;
  }
}

.presentation {
  padding: 100px 0;
  @include mobile{
    padding: 50px 0;
  }
  // .presentation__body

  &__body {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 415px);
    gap: 10px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .presentation__content

  &__content {
  }

  // .presentation__title

  &__title {
    margin: 0;
    margin-bottom: 5px;
  }

  // .presentation__img

  &__img {
  }

  // .presentation__info

  &__info {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  // .presentation__item

  &__item {
    display: grid;
    grid-template-columns: 1fr 30px;
    padding: 20px;
    border: 1px solid black;
    gap: 0 10px;
  }

  // .presentation__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 17px;
  }

  // .presentation__item-p

  &__item-p {
    line-height: 124%;
    margin: 0;
    max-width: 254px;
  }

  // .presentation__item-block

  &__item-block {
    width: 30px;
    height: 30px;

    background-color: #06828e;
  }

  // .presentation__item--black

  &__item--black {
    background-color: var(--black-color);
    color: white;

      .presentation__item-block{
        background-color: white;
      }
  }
}

.tehno {
  padding: 40px 0;
  // .tehno__body-form

  &__body-form {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 420px);
    gap: 90px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .tehno__body-content

  &__body-content {
    display: flex;
    flex-direction: column;
    @include tablet{
      order: 2;
    }
  }

  // .tehno__body-content-img

  &__body-content-img {
    display: none;
    &--active{
      display: block;
    }
  }

  // .tehno__body-content-inputs

  &__body-content-inputs {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
    margin-top: auto;
    @include tablet{
      margin-bottom: 50px;
    }
  }

  // .tehno__body-content-input

  &__body-content-input {
    width: 100%;
    color: rgba(0, 0, 0, 0.7);
    padding-bottom: 7px;
    padding-left: 10px;
    border-bottom: 1px solid black;

    &::placeholder{
      color: rgba(0, 0, 0, 0.2);
    }
  }

  &__body-info-item{
    display: none;
    &--active{
      display: block;
    }
  }

  // .tehno__body-info

  &__body-info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // .tehno__body-info-title

  &__body-info-title {
    font-weight: 700;
    @include fluid-text(64, 32);
    line-height: 150%;
    margin: 0;
  }

  // .tehno__body-info-text

  &__body-info-text {
    margin: 0;
    margin-bottom: 50px;
    a{
      color: black;
      text-decoration: underline;
      @include hover{
        color: var(--accent-color);
      }
    }
  }

  &__body-nav{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 60px;
  }

  &__body-btn{
    display: flex;
    align-items: center;
    gap: 5px;
    .tehno__body-btn-img{
      display: none;
    }
    &--active{
      .tehno__body-btn-img{
        display: block;
      }
      span{
        background-color: var(--accent-color);
        color: var(--light-color);
      }
    }
    span{
      font-family: var(--font-family-accent);
      font-weight: 700;
      font-size: 28px;
      padding: 5px 10px;
      border-radius: 15px;

    }
  }

  // .tehno__body-info-img

  &__body-info-img {
  }

  // .tehno__body-info-accent

  &__body-info-accent {
  }

  // .tehno__body-submit

  &__body-submit {
    margin-top: auto;
    color: #fff;
    background-color: var(--accent-color);
    width: 100%;
    padding: 15px 10px;
    border-radius: 10px;
    @include hover{
      background-color: var(--black-color);
    }
  }
}

.reviews {
  padding: 50px 0;
  // .reviews__body

  &__body {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 390px);
    gap: 75px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .reviews__content

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 70px;
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .reviews__info

  &__info {
  }

  // .reviews__title

  &__title {
    margin: 0;
    margin-bottom: 70px;
  }

  // .reviews__content-info

  &__content-info {
    display: grid;
    grid-template-columns: 1fr;
    gap: 40px;
    @include mobile{
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .reviews__content-item

  &__content-item {
  }

  // .reviews__content-item--row

  &__content-item--row {
    display: flex;
    gap: 10px;
    @include mobile{
      gap: 0;
      flex-direction: column;
    }
  }

  // .reviews__content-item-value

  &__content-item-value {
    font-weight: 700;
    font-size: 36px;
    line-height: 116%;
    color: var(--black-color);
    margin-bottom: 10px;
  }

  // .reviews__content-item-deskr

  &__content-item-deskr {
  }

  // .reviews__content-item-text

  &__content-item-text {
  }

  // .reviews__swiper-body

  &__swiper-body {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  // .reviews__swiper

  &__swiper {
    margin-bottom: 25px;

    .swiper-slide{
      @include tablet{
        display: flex;
        justify-content: center;
      }
    }

  }

  // .reviews__swiper-item

  &__swiper-item {
    position: relative;
  }

  &__swiper-item-video {
    height: 580px;
    border-radius: 20px;

  }

  // .reviews__swiper-img

  &__swiper-img {
    display: block;

  }

  &__swiper-play{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
  }

  // .reviews__swiper-actions

  &__swiper-actions {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  // .reviews__swiper-prev

  &__swiper-prev {
    position: static;
    margin: 0;
    height: 50px;
    width: 50px;
    @include hover{
      circle{
        fill: black;
      }
    }
    &::after{
      display: none;
    }
  }

  // .reviews__swiper-pagination

  &__swiper-pagination {
    font-weight: 100;
    font-size: 21px;
    color: var(--black-color);
    min-width: 50px;
    text-align: center;
    display: flex;
    justify-content: center;
  }

  // .reviews__swiper-next

  &__swiper-next {
    position: static;
    margin: 0;
    height: 50px;
    width: 50px;
    @include hover{
      circle{
        fill: black;
      }
    }
    &::after{
      display: none;
    }
  }

  // .reviews__certificates

  &__certificates {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__certificates-col{
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  // .reviews__certificates-img

  &__certificates-img {

  }
}

.videos {
  padding: 50px 0;

  &__title{
    line-height: 116%;
  }
  // .videos__body

  &__body {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 375px);
    align-items: flex-start;
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .videos__main

  &__main {
    position: relative;
    height: 100%;
  }

  // .videos__main-content

  &__main-content {
    position: static;
    top: 50px;
    left: 30px;
    color: var(--black-color);
    @include mobile{
      top: 15px;
      bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-bottom: 20px;
    }
  }

  // .videos__main-title

  &__main-title {
    line-height: 116%;
    margin: 0;
    margin-bottom: 27px;
    @include mobile{
      font-size: 18px;
    }
  }

  // .videos__main-subtitle

  &__main-subtitle {

  }

  // .videos__main-video

  &__main-video {
    display: block;
    width: 100%;
    height: 100%;
    @include tablet{
      height: 500px;
    }
    @include small-tablet{
      height: 300px;
    }
    @include mobile{
      height: 250px;
    }
  }

  // .videos__main-play

  &__main-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  // .videos__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    @include tablet{
      flex-direction: row;
      justify-content: space-between;
      gap: 5px;
    }
    @include mobile{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      row-gap: 20px;
    }
  }

  // .videos__item

  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    @include tablet{
      flex-direction: column;
      width: 100%;
    }
  }

  // .videos__item-video-wrapper

  &__item-video-wrapper {
    position: relative;
  }

  // .videos__item-video

  &__item-video {
    border-radius: 15px;
    display: block;
    width: 100%;
    height: 100%;
    max-width: 166px;
    max-height: 94px;
  }

  // .videos__item-play

  &__item-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  // .videos__item-info

  &__item-info {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  // .videos__item-title

  &__item-title {
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    color: var(--black-color);
    margin: 0;
    margin-bottom: 5px;
    max-width: 170px;
    @include small-tablet{
      display: -webkit-box;
      -webkit-line-clamp: 1; /*   1  */
      -webkit-box-orient: vertical;
      overflow: hidden;
      font-size: 10px;
    }
  }

  // .videos__item-subtitle

  &__item-subtitle {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--black-color);
    display: -webkit-box;
    -webkit-line-clamp: 2; /*   2  */
    -webkit-box-orient: vertical;
    overflow: hidden;
    max-width: 170px;
    @include small-tablet{
      font-size: 10px;
    }
  }
}

.delivery {
  padding: 35px 0 60px 0;
  // .delivery__body

  &__body {
    position: relative;
  }

  // .delivery__tabs

  &__tabs {
  }

  // .delivery__nav

  &__nav {
    display: inline-flex;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 40px;
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
  }

  &__nav-btn {
    font-family: var(--font-family-accent);
    font-weight: 600;
    line-height: 124%;
    color: var(--black-color);
    padding: 18px 72px;
    border-radius: 40px;
    @include mobile{
      padding: 18px 50px;
    }
    &.tabs__nav-btn--active{
      background-color: var(--light-color);
      box-shadow: 1px 0 4px 0 rgba(0, 0, 0, 0.25);
    }
  }

  // .delivery__nav-item

  &__nav-item {

  }

  // .delivery__content

  &__content {

  }

  // .delivery__panel

  &__panel {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: flex-end;
    gap: 60px;
    @include tablet{
      grid-template-columns: 1fr;
      align-items: flex-start;
    }
  }

  // .delivery__info

  &__info {
    padding-top: 100px;
  }

  // .delivery__title

  &__title {
    margin: 0;
    margin-bottom: 23px;
  }

  // .delivery__notation


  &__notation {
    font-weight: 600;
    padding: 5px 18px;
    background-color: #06828e;
    display: inline-block;
    margin-bottom: 15px;
    color: var(--light-color);
    border-radius: 13px;
  }

  // .delivery__location

  &__location {
    a{
      @include hover{
        color: var(--accent-color);
      }
    }
  }

  // .delivery__text

  &__text {
    line-height: 153%;
    margin-bottom: 25px;
      a{
        font-weight: 600;
        color: var(--black-color);
      }
  }

  // .delivery__methods

  &__methods {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 10px;
    @include tablet{
      grid-template-columns: 1fr 1fr;
    }
  }

  // .delivery__methods-link

  &__methods-link {
    min-height: 90px;
    display: flex;
    align-items: center;
    gap: 15px;
    padding: 10px;
    border: 1px solid var(--accent-color);
    border-radius: 13px;
    color: var(--black-color);
    @include mobile{
      flex-direction: column;
      justify-content: space-between;
    }
    @include hover{
      background-color: var(--accent-color);
      color: var(--light-color);
      svg{
        *{
          fill: var(--light-color)
        }
      }
    }
  }

  // .delivery__methods-text

  &__methods-text {
    font-weight: 600;
    font-size: 14px;
    line-height: 150%;
    color: inherit;
  }

  // .delivery__map

  &__map {
    width: 100%;
    height: 100%;
  }
}

.faq {
  padding: 40px 0;
  color: var(--light-color);
  background-color: #15222a;;
  // .faq__title

  &__title {
    margin: 0;
    margin-bottom: 35px;
  }

  // .faq__body

  &__body {
    display: grid;
    grid-template-columns: minmax(200px, 460px) 1fr;
    gap: 20px;
    align-items: flex-start;
    margin-bottom: 20px;

    @include tablet{
      grid-template-columns: 1fr 1fr;
    }
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  &__tabs{

  }

  // .faq__category

  &__category {
    border: 1px solid rgba(255, 255, 255, 0.42);
    border-radius: 15px;
    padding: 30px;

    .tabs__nav{
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .tabs__nav-btn{
      color: var(--light-color);
      text-align: left;
      width: 100%;
      font-weight: 400;
      font-family: var(--font-family-accent);
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;

      line-height: 218%;
      padding: 8px 16px;
      border-radius: 10px;

      background: rgba(255, 255, 255, 0.27);

      @include hover{
        background-color: var(--accent-color);
      }
      &--active{
        background-color: var(--accent-color);
      }
    }

  }

  // .faq__category-title

  &__category-title {
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--font-family-accent);
    line-height: 154%;
    color: rgba(255, 255, 255, 0.6);
  }

  // .faq__content

  &__content {
    padding: 30px;
    background: linear-gradient(328deg, rgba(255, 255, 255, 0.13) 0%, rgba(153, 153, 153, 0.13) 100%);
    border-radius: 15px;
  }

  // .faq__content-title

  &__content-title {
    font-family: var(--font-family-accent);
    margin: 0;
    margin-bottom: 30px;
    color: rgba(255, 255, 255, 0.6);
  }

  // .faq__additionally

  &__additionally {
    padding: 30px;
    background: linear-gradient(328deg, rgba(255, 255, 255, 0.13) 0%, rgba(102, 242, 255, 0.13) 45%, rgba(153, 153, 153, 0.13) 100%);
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    @include small-tablet{
      flex-direction: column;
    }
  }

  // .faq__additionally-info

  &__additionally-info {
    @include small-tablet{
      text-align: center;
    }
  }

  // .faq__additionally-title

  &__additionally-title {
    margin: 0;
    line-height: 154%;
    margin-bottom: 16px;
  }

  // .faq__additionally-subtitle

  &__additionally-subtitle {
    color: rgba(255, 255, 255, 0.6);
  }

  // .faq__additionally-contact

  &__additionally-contact {
    display: flex;
    gap: 10px;

    @include mobile{
      flex-wrap: wrap;
      justify-content: center;
      gap: 20px;
    }
  }

  // .faq__additionally-social

  &__additionally-social {
    svg{
      rect{
       transition: all .2s ease-in;
      }
    }
    @include hover{
      svg{
        rect{
          fill: var(--accent-color);
        }
      }
    }
    svg{
      display: block;
    }
  }

  // .faq__additionally-contact-link

  &__additionally-contact-link {
    line-height: 218%;
    color: var(--light-color);

    display: flex;
    align-items: center;
    gap: 40px;
    background-color: var(--accent-color);
    padding: 4px 16px 4px 28px;
    border-radius: 10px;

    text-wrap: nowrap;

    @include hover{
      background-color: var(--black-color);
    }

    @include mobile{
      width: 100%;
      justify-content: center;
      gap: 15px;
    }
  }
}

.faq-additionally{
  background-color: white;
  margin-bottom: 100px;
    &.faq-additionally--mb-0{
      margin-bottom: 0;
    }
  @include mobile{
    margin-bottom: 50px;
  }
  .faq__additionally{
    background: #23363e;
  }

