.accordion {
  display: flex;
  flex-direction: column;
  // .accordion__item

  &__item {
    display: flex;
    flex-direction: column;
    &:not(:last-child){
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      margin-bottom: 10px;
      padding-bottom: 10px;
    }

    .accordion__content{
      height: 0;
      overflow: hidden;
    }
  }

  // .accordion__content

  &__content {
    display: flex;
    flex-direction: column;
    transition: height .2s ease-in;
  }

  // .accordion__text

  &__text {
    color: rgba(255, 255, 255, 0.6);
    font-family: var(--font-family-accent);
    margin: 0;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  // .accoridon__head

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    color: var(--light-color);
    text-align: left;
    @include hover{
     color: var(--accent-color);
    }
  }

  // .accoridon__head-ques

  &__head-ques {
    font-family: var(--font-family-accent);
    font-weight: 400;
    line-height: 160%;
  }

  // .accoridon__head-indicator

  &__head-indicator {
    font-weight: 400;
    font-size: 24px;
  }
}

.documentation {
  background-color: #15222a;
  color: var(--light-color);
  margin-bottom: 40px;
  padding: 45px 0;
  // .documentation__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  // .documentation__body

  &__body {
    display: grid;
    grid-template-columns: 1fr minmax((100px, 250px));
    gap: 42px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .documentation__btns

  &__btns {
    .tabs__nav{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      @include mobile{
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }

  // .documentation__nav-btn

  &__nav-btn {
    font-family: var(--font-family-accent);
    font-weight: 400;
    line-height: 218%;
    color: var(--light-color);
    width: 100%;
    text-align: left;
    padding: 8px 16px;
    border-radius: 10px;
    border: 1px solid #545e64;
    background-color: rgba(255, 255, 255, 0.27);
    @include hover{
      background-color: var(--accent-color);
      border: 1px solid var(--accent-color);
    }
    &.tabs__nav-btn--active{
      background-color: var(--accent-color);
      border: 1px solid var(--accent-color);
    }
  }

  // .documentation__content-title

  &__content-title {
    margin-bottom: 20px;
  }

  // .documentation__content

  &__content {
    display: flex;
    flex-direction: column;
    gap: 25px;

  }

  // .documentation__item

  &__item {
    display: flex;
    align-items: flex-start;
    gap: 10px;
  }

  // .documentation__item-img

  &__item-img {
    min-width: 34px;
    min-height: 34px;
    border-radius: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--accent-color);

    position: relative;

    svg{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%)
    }
  }

  // .documentation__item-info

  &__item-info {
  }

  // .documentation__item-title

  &__item-title {
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    margin-bottom: 10px;
  }

  // .documentation__item-download

  &__item-download {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    color: var(--accent-color);
    @include hover{
      color: var(--light-color);
      svg{
        *{
          stroke: var(--light-color);
        }
      }
    }

  }
}
