.planner {
  margin-bottom: 100px;
  background-color: #fbfbfb;
  @include mobile{
    margin-bottom: 50px;
  }
  // .designers__content

  &__content {
    grid-template-columns: minmax(200px, 500px) 1fr;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .designers__info

  &__info {
  }

  // .designers__title

  &__title {
    letter-spacing:2px;
    font-size: 28px;
  }

  // .designers__subtitle

  &__subtitle {
  }

  // .designers__application

  &__application {
  }

  // .designers__actions

  &__actions {
    background-color: white;
  }

  // .designers__actions-links

  &__actions-links {
    backdrop-filter: unset;
    background-color: unset;
  }

  // .designers__actions-link

  &__actions-link {
    background-color: #ededed;
  }

  // .designers__actions-img

  &__actions-img {
  }
}

.application-consultation {
  background-color: #ededed;
  padding: 100px 0;
  @include mobile{
    padding: 50px 0;
  }
  // .application-consultation__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  // .application-consultation__content

  &__content {
    display: grid;
    grid-template-columns: minmax(100px, 200px) 1fr;
    gap: 100px;
    @include tablet{
      gap: 30px;
      flex-direction: column;
      grid-template-columns: 1fr;
    }
  }

  // .application-consultation__consultant

  &__consultant {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }

  // .application-consultation__consultant-img

  &__consultant-img {
    border-radius: 100%;
    margin-bottom: 22px;
  }

  // .application-consultation__consultant-name

  &__consultant-name {
    margin: 0;
    margin-bottom: 5px;
  }

  // .pplication-consultation__consultant-post

  &__consultant-post {
    font-size: 16px;
  }

  // .application-consultation__form

  &__form {
    display: grid;
    grid-template-columns: minmax(200px, 255px) 1fr;
    gap: 10px 20px;
    @include mobile{
      display: flex;
      flex-direction: column;
    }
  }

  // .application-consultation__form-input

  &__form-input {
    width: 100%;
    display: block;
    border-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: var(--light-color);
    padding: 12px 15px;
    &::placeholder{
      color: rgba(0, 0, 0, 0.6);
    }
  }

  // .application-consultation__form-textarea

  &__form-textarea {
    grid-row: 1/4;
    grid-column: 2/3;
    @include mobile{
      min-height: 150px;
    }
  }

  // .application-consultation__form-agreement

  &__form-agreement {
    margin-top: 32px;
    grid-column: 2/3;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 80px;
    @include tablet{
      flex-direction: column;
      gap: 30px;
      align-items: unset;
      grid-column: 2 span;
    }
  }

  // .application-consultation__form-submit

  &__form-submit {
    padding: 15px 70px;
  }

}



