
.designers-staff {
  padding: 100px 0;
  background-color: #15222a;
  position: relative;
  @include mobile{
    padding: 50px 0 80px 0;
  }
  // .designers-staff__head

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 20px;
    margin-bottom: 30px;
    @include tablet{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  // .designers-staff__title

  &__title {
    font-family: var(--font-family-accent);
    color: var(--light-color);
    margin: 0;
  }

  // .designers-staff__actions

  &__actions {
    display: flex;
    align-items: center;
    gap: 40px;
    @include tablet{
      width: 100%;
      column-gap: 10px;
    }
  }

  // .designers-staff__actions-swiper-btns

  &__actions-swiper-btns {
    display: flex;
    align-items: center;
    gap: 10px;
    @include tablet{
      order: 2;
      margin-left: auto;
    }
    @include mobile{

    }
    .designers-staff-prev, .designers-staff-next{
      position: static;
      margin: 0;
      width: 40px;
      height: 40px;
      @include hover{
        svg{
          *{
            stroke: var(--black-color)
          }
        }
      }
      &::after{
        display: none;
      }
    }

    .designers-staff-prev{

    }

    .designers-staff-next{

    }
  }

  // .designers-staff__head-link

  &__head-link {
    font-weight: 400;
    padding: 15px 60px;
    @include mobile{
      padding: 15px 20px;
    }
  }

  // .designers-staff__swiper

  &__swiper {
  }

  // .designers-staff__swiper-slide

  &__swiper-slide {
    display: flex;
    flex-direction: column;
  }

  // .designers-staff__img

  &__img {
    width: 100%;
    max-height: 306px;
    border-radius: 10px;
    margin-bottom: 15px;
  }

  // .designers-staff__name

  &__name {
    font-weight: 700;
    color: var(--light-color);
    margin-bottom: 13px;
  }

  // .designers-staff__link

  &__link {
    font-weight: 400;
    font-size: 16px;
    text-decoration: underline;
    color: var(--light-color);
    @include hover{
      color: var(--accent-color);
    }
  }
}

.designers-project {
  padding: 100px 0;
  @include mobile{
    padding: 50px 0;
  }
  // .designers-project__title

  &__title {
    margin: 0;
    margin-bottom: 30px;
  }

  // .designers-project__form

  &__form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px 35px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .designers-project__form-input

  &__form-input {
    padding: 12px 15px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    &::placeholder{
      color: rgba(0, 0, 0, 0.6);
    }
  }

  &__form-input-email {
    grid-column: 1/3;
    @include tablet{
      grid-column: unset;
    }
  }

  // .designers-project__form-textarea

  &__form-textarea {
    grid-column: 3/5;
    grid-row: 1/3;
    @include tablet{
      grid-row: unset;
      grid-column: unset;
    }
  }

  // .designers-project__form-file-label

  &__form-file-label {
  }

  // .designers-project__form-file-input

  &__form-file-input {
  }

  // .designers-project__form-file-text

  &__form-file-text {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    gap: 10px;
    position: relative;
    padding-bottom: 8px;
    border-bottom: 1px solid var(--black-color);
    transition: all .2s ease-in;
    @include hover{
      color: var(--accent-color);
    }
  }

  // .designers-project__form-agreement

  &__form-agreement {
    grid-column: 3/5;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @include tablet{
      grid-row: unset;
      grid-column: unset;
      grid-template-columns: 1fr;
    }
  }

  // .designers-project__form-submit

  &__form-submit {

  }
}


.visual-poster {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .3d-poster__wrapper

  &__wrapper {
    background: linear-gradient(90deg, #15222a 0%, #818e9d 100%);
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding: 45px 30px;
    position: relative;
    @include small-tablet{
      flex-direction: column;
    }
  }

  // .3d-poster__text

  &__text {
    font-size: 19px;
    font-weight: 700;
    color: var(--light-color);
    display: flex;
    align-items: center;
    gap: 13px;
    @include mobile{
      flex-direction: column;
      text-align: center;
    }
  }

  // .3d-poster__logo

  &__logo {
  }

  &__img-wrapper {
    flex: 1 1 271px;
    @include tablet{
      display: none;
    }
  }

  // .3d-poster__img

  &__img {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

  }

  // .3d-poster__link

  &__link {
    padding: 15px 100px;
    @include mobile{
      width: 100%;
      padding: 15px;
      text-align: center;
    }
  }
}

.events {
  padding: 100px 0;
  background-color: #15222a;
  @include mobile{
    padding: 50px 0;
    margin-bottom: 50px;
  }
  // .events__title

  &__title {
    color: var(--light-color);
    margin: 0;
    margin-bottom: 30px;
  }

  // .events__content

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .events__item

  &__item {
    display: flex;
    flex-direction: column;
    @include hover{
      .events__item-link{
        color: var(--accent-color);
        svg{
          *{
            stroke: var(--accent-color);
          }
        }
      }
    }
  }

  // .events__item-img

  &__item-img {
    border-radius: 10px;
    margin-bottom: 10px;
  }

  // .events__item-link

  &__item-link {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 30px;
    font-weight: 700;
    color: var(--light-color);
  }

  // .events__item-date

  &__item-date {
    color: var(--light-color);
    font-weight: 400;
    font-size: 16px;
    line-height: 167%;
    margin-top: 6px;
  }
}

.team {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .team__title

  &__title {
    margin: 0;
    margin-bottom: 20px;
  }

  // .team__subtitle

  &__subtitle {
    margin-bottom: 60px;
  }

  // .team__content

  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px;
    max-width: 1000px;
    margin: 0 auto;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .team__item

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  // .team__item-img

  &__item-img {
    margin-bottom: 20px;
  }

  // .team__item-name

  &__item-name {
    font-weight: 700;
    margin-bottom: 5px;
  }

  // .team__item-post

  &__item-post {
    font-size: 16px;
  }
}
