.product-info {
  margin-bottom: 50px;
  // .product-info__wrapper

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr minmax(100px, 200px);
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
    .tabs__nav{
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      margin-bottom: 12px;
      @include mobile{
        display: grid;
        grid-template-columns: 1fr 1fr;
      }
    }

    .tabs__nav-btn{
      display: flex;
      align-items: center;
      gap: 7px;
      font-size: 16px;
      color: #6b7170;
      padding: 12px 20px;
      border: 1px solid #e0e0e0;
      border-radius: 7px;
      background-color: var(--light-color);
      @include hover{
        background-color: #f1f1f1;
        color: #1d1d1d;;
        svg{
          path{
            fill: #1D1D1D
          }
        }
      }
      svg{
        flex: none;
      }
      @include mobile{
        font-size: 13px;
        width: 100%;
        height: 100%;
      }
      &--active{
        background-color: #f1f1f1;
        color: #1d1d1d;;
        svg{
          path{
            fill: #1D1D1D
          }
        }
      }
    }
  }

  // .product-info__panel

  &__panel {
    background-color: #f1f1f1;
    padding: 20px 30px;
    border-radius: 13px;
    font-size: 16px;
    color: #1d1d1d;
    min-height: 287px;
    @include mobile{
      font-size: 14px;
    }
  }

  // .product-info__teh

  &__teh {
    line-height: 160%;
    display: flex;
    flex-direction: column;
    gap: 30px;

  }

  // .product-info__teh-list

  &__teh-list {
    display: flex;
    flex-direction: column;
  }

  // .product-info__teh-item

  &__teh-item {
    display: flex;
    @include small-tablet{
      font-size: 14px;
    }
    @include mobile{
      flex-direction: column;
    }
  }

  // .product-info__teh-name

  &__teh-name {
    flex: 1 1 auto;
    overflow: hidden;
    position: relative;
    max-width: 450px;
    @include mobile{
      font-weight: 600;
    }
    &::after{
      display: inline;
      position: absolute;
      content: "_________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________________";
      @include mobile{
        display: none;
      }
    }

  }

  // .product-info__teh-value

  &__teh-value {
    max-width: 450px;
  }

  // .product-info__teh-additionally

  &__teh-additionally {

  }

  // .product-info__delivery

  &__delivery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .product-info__delivery-block

  &__delivery-block {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  // .product-info__delivery-title

  &__delivery-title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 17px;
    margin: 0;
    margin-bottom: 10px;
  }

  // .product-info__delivery-list

  &__delivery-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .product-info__delivery-item

  &__delivery-item {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  // .product-info__delivery-link

  &__delivery-link {
    font-weight: 600;
    color: var(--black-color);
    margin-top: auto;
    padding-top: 10px;
    @include hover{
      color: var(--accent-color);
    }
  }

  // .product-info__delivery-additionally

  &__delivery-additionally {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 600;
  }

  // .product-info__montage

  &__montage {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .product-info__montage-block

  &__montage-block {
  }

  // .product-info__montage-title

  &__montage-title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 17px;
    margin: 0;
    margin-bottom: 15px;
  }

  // .product-info__montage-list

  &__montage-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .product-info__montage-item

  &__montage-item {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  // .product-info__docs

  &__docs {
    color: #1d1d1d;
  }

  // .product-info__docs-title

  &__docs-title {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    margin: 0;
    margin-bottom: 30px;
  }

  // .product-info__docs-list

  &__docs-list {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  // .product-info__docs-item

  &__docs-item {
    display: flex;
    flex-direction: column;

  }

  // .product-info__docs-item-name

  &__docs-item-name {
    display: flex;
    align-items: flex-start;
    font-size: 14px;
    gap: 7px;
    margin-bottom: 10px;
    svg{
      flex: none;
    }
  }

  // .product-info__docs-item-file

  &__docs-item-file {
    display: flex;
    align-items: center;
    gap: 10px;
    color: var(--accent-color);
    text-align: right;
    font-size: 14px;
    align-self: flex-end;
    @include hover{
      color: var(--black-color);
      svg{
        path{
          stroke: var(--black-color)
        }
      }
    }
    @include tablet{
      align-self: flex-start;
    }
  }
}

.product-reviews {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .product-reviews__head

  &__head {
    display: flex;
    align-items: center;
    gap: 70px;
    margin-bottom: 40px;
    @include small-tablet{
      flex-direction: column;
      align-items: flex-start;
      gap: 20px;
    }
  }

  // .product-reviews__title

  &__title {
    margin: 0;
  }

  // .product-reviews__write

  &__write {
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--black-color);
    gap: 8px;
    padding-bottom: 3px;
    color: var(--black-color);
    font-weight: 600;
    @include hover{
      color: var(--accent-color);
      border-bottom: 1px solid var(--accent-color);
      svg{
        path{
          stroke: var(--accent-color)
        }
      }
    }
  }

  // .product-reviews__btns-nav

  &__btns-nav {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-left: auto;
  }

  // .product-reviews__btn-prev

  &__btn-prev, &__btn-next {
    &.swiper-button-disabled{
      opacity: 0.5;
    }
    @include hover{
      svg{
        path{
          stroke: var(--accent-color);
        }
      }
    }
  }

  &__btn-prev {
  }

  // .product-reviews__btn-next

  &__btn-next {
  }

  // .product-reviews__swiper

  &__swiper {
    padding: 15px;
    margin: -15px;
    .swiper-slide{
      display: flex;
      height: auto;
    }
  }

  // .product-reviews__item

  &__item {
    padding: 25px;
    box-shadow: 2px 2px 19px 0 rgba(55, 55, 55, 0.15);
    border-radius: 15px;
    display: block;
    color: var(--black-color);
    display: flex;
    flex-direction: column;
  }

  // .product-reviews__item-head

  &__item-head {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
  }

  // .product-reviews__item-name

  &__item-name {
    font-weight: 500;
  }

  // .product-reviews__item-date

  &__item-date {
    font-size: 14px;
    color: #adadad;

  }

  // .product-reviews__item-text

  &__item-text {
    margin: 0;
    margin-bottom: 30px;
    font-size: 14px;
  }

  // .product-reviews__media

  &__media {
    display: flex;
    justify-content: space-between;
    gap: 15px;
    margin-top: auto;
    &:has([data-media="5"]){
      .product-reviews__media-wrapper{
        display: none;
      }
      [data-media="1"]{
        display: block;
      }
      [data-media="2"]{
        display: block;
      }
      [data-media="3"]{
        display: block;
      }
      [data-media="4"]{
        display: block;
        .product-reviews__media-wrapper-blur{
          filter: blur(1.600000023841858px);
        }
        .product-reviews__media-more-value{
          display: block;
        }
        &::after{
          display: block;
          content: "";
          position: absolute;
          inset: 0;
          background-color: rgba(255, 255, 255, 0.5);
        }
      }
    }
    @include mobile{
      &:has([data-media="4"]){
        .product-reviews__media-wrapper{
          display: none;
        }
        [data-media="1"]{
          display: block;
        }
        [data-media="2"]{
          display: block;
        }
        [data-media="3"]{
          display: block;
          .product-reviews__media-wrapper-blur{
            filter: blur(1.600000023841858px);
          }
          .product-reviews__media-more-value{
            display: block;
          }
          &::after{
            display: block;
            content: "";
            position: absolute;
            inset: 0;
            background-color: rgba(255, 255, 255, 0.5);
          }
        }

      }
    }
  }

  // .product-reviews__media-wrapper

  &__media-wrapper {
    position: relative;
    overflow: hidden;
    flex: 1 1 auto;

  }

  // .product-reviews__media-img

  &__media-img {
    border-radius: 5px;
    width: 100%;
  }

  &__media-more-value{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--black-color);
    font-weight: 500;
    font-size: 18px;
    z-index: 2;
    display: none;
  }
}

