.order-steps {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .order-steps__title

  &__title {
    margin-bottom: 35px;
  }

  // .order-steps__content

  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      gap: 10px;
    }
  }

  // .order-steps__item

  &__item {
    display: flex;
    flex-direction: column;
    gap: 35px;
    padding: 25px;
    border-radius: 20px;
    background-color:#3e4952;
    color: var(--light-color);
    @include mobile{
      gap: 20px;
      padding: 20px;
    }
  }

  // .order-steps__item-num

  &__item-num {
    font-weight: 500;
    font-size: 30px;

  }

  // .order-steps__item-text

  &__item-text {
    font-size: 16px;
    @include mobile{
      font-size: 14px;
    }
    span{
      font-weight: 600;
      display: inline;
    }
  }

  // .order-steps__item-final

  &__item-final {
    grid-column: 3/5;
    grid-row: 1/3;
    border-radius: 20px;
    background-color: var(--accent-color);
    padding: 25px;
    color: var(--light-color);
    display: flex;
    flex-direction: column;
    gap: 20px;
    @include tablet{
      grid-row: unset;
      grid-column: 1/3;
    }
    @include mobile{
      padding: 20px;
    }
  }

  // .order-steps__item-img-wrapper

  &__item-img-wrapper {
    position: relative;
  }

  // .order-steps__item-img

  &__item-img {
    width: 100%;
  }

  // .order-steps__btn

  &__btn {
    font-weight: 500;
    font-size: 16px;
    padding: 12px 32px;
    background-color: var(--light-color);
    border-radius: 13px;
    position: absolute;
    bottom: 0;
    right: 0;
    display: inline-block;
    @include mobile{
      position: static;
      margin-top: 15px;
    }
    @include hover{
      background-color: var(--black-color);
      color: var(--light-color);
    }
  }
}

