.product-main {
  margin-bottom: 50px;
  // .product-main__content

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: flex-start;
    gap: 40px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .product-main__demonstration

  &__demonstration {
    min-width: 100%;
    width: 100%;
    gap: 15px;
    display: flex;

  }

  // .product-main__swiper-main

  &__swiper-main {
    height: auto;
    position: relative;
    .swiper-slide{
      display: flex;
      img{
        border-radius: 5px;
        display: block;
        height: 100%;
      }
    }
  }

  &__swiper-main-status{
    position: absolute;
    top: 25px;
    right: 25px;
  }

  // .product-main__swiper-thumbs

  &__swiper-thumbs {
    height: clamp(18.75rem, -13.356rem + 66.97vw, 36.875rem);
    min-width: 80px;
    display: flex;
    @include small-tablet{
      height: 400px;
      min-width: 60px;
    }
    @include mobile{
      height: 300px;
      min-width: 80px;
    }
    img{
      display: block;
      height: 100%;
      border-radius: 3px;
    }
    .swiper-wrapper{

    }
    .swiper-slide{

      flex-shrink: unset;
        div{
          display: flex;
          width: 100%;
          height: 100%;
        }
        img{
          border-radius: 3px;
          filter: grayscale(1);
          border: 1px solid transparent;
          transition: all .2s ease-in;
          @include hover{
            border: 1px solid #000;
            filter: unset;
          }
        }
    }
    .swiper-slide-thumb-active{
      img{
        border: 1px solid #000;
        filter: unset;
      }

    }
  }

  // .product-main__info

  &__info {
    flex: 1 1 auto;
  }

  // .product-main__info-title

  &__info-title {
    font-weight: 600;
    font-size: 27px;
    margin: 0;
    margin-bottom: 10px;
    line-height: 135%;
  }

  // .product-main__info-head

  &__info-head {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 15px;
  }

  // .product-main__info-stars

  &__info-stars {
    display: flex;
  }

  // .product-main__reviews-link

  &__reviews-link {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    color: #797979;
    span{
      text-decoration: underline;
    }
  }

  // .product-main__info-articly

  &__info-articly {
    font-size: 14px;
    color: #797979;
  }

  &__info-garant {
