  }

  // .product-main__calculator-block-title

  &__calculator-block-title {
    font-family: var(--font-family);
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 10px;
  }

  // .product-main__calculator-block-row

  &__calculator-block-row {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
  }

  // .product-main__calculator-block-label

  &__calculator-block-label {
    display: flex;
    align-items: center;
    gap: 7px;
    font-weight: 500;
    font-size: 12px;
  }

  // .product-main__calculator-block-input

  &__calculator-block-input {
  }

  // .product-main__calculator-additionally

  &__calculator-additionally {
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    color: #797979;
    margin-bottom: 15px;
  }

  // .product-main__calculator-price-wrapper

  &__calculator-price-wrapper {
    display: flex;
    gap: 35px;
  }

  // .product-main__calculator-price

  &__calculator-price {
  }

  // .product-main__calculator-price-text

  &__calculator-price-text {
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 2px;
  }

  // .product-main__calculator-price-value

  &__calculator-price-value {
    display: flex;
    gap: 10px;
    font-size: 12px;
    span{
      color: var(--accent-color);
      font-weight: 600;
      font-size: 16px;
    }
  }

  // .product-main__calculator-counter

  &__calculator-counter {
    height: 40px;
    padding: 2px 20px;
    background-color: #f1f1f1;
    border: 1px solid #d9d9d9;
    border-radius: 3px;
    display: flex;
    align-items: center;
    gap: 27px ;
  }

  // .product-main__calculator-minus

  &__calculator-minus {
    width: 10px;
    font-size: 20px;
  }

  // .product-main__calculator-num

  &__calculator-num {
  }

  // .product-main__calculator-plus

  &__calculator-plus {
    width: 10px;
    font-size: 20px;
  }

  // .product-main__calculator-form-actions

  &__calculator-form-actions {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px 50px;
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .product-main__calculator-form-submit

  &__calculator-form-submit {
  }

  // .product-main__calculator-buy-now

  &__calculator-buy-now {
    font-weight: 500;
    font-size: 18px;
    color: var(--accent-color);
    text-decoration: underline;
    @include hover{
      color: var(--black-color);
    }
  }
}

