.manufacture-hero {
  background-image: url(../img/manufacture-hero-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
    min-height: 400px;
  }
  // .manufacture-hero__title

  &__title {
    padding: 75px 20px 20px 55px;
    color: var(--light-color);
    font-weight: 700;
    font-size: 64px;
    line-height: 122%;
    margin: 0;
    @include mobile{
      font-size: 32px;
      padding: 75px 20px 20px 20px;
    }
    span{
      display: inline;
      padding: 15px;
      text-transform: uppercase;
      background-size: 100% 100%;

    }
  }
}

.manufacture {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .manufacture__title

  &__title {
    margin: 0;
    margin-bottom: 25px;
  }

  // .manufacture__content

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 35px;
  }

  // .manufacture__content-text

  &__content-text {
    font-size: 25px;
    line-height: 130%;
    max-width: 860px;
    margin: 0;
    @include small-tablet{
      font-size: 16px;
    }
  }

  // .manufacture__content-logo

  &__content-logo {
    @include small-tablet{
      display: none;
    }
  }

  // .manufacture__stats

  &__stats {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
    @include small-tablet{
      gap: 15px;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .manufacture__stat

  &__stat {
    display: flex;
    flex-direction: column;
    gap: 25px;
    padding: 20px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    background-color: var(--light-color);
    border-radius: 15px;
  }

  // .manufacture__stat-value

  &__stat-value {
    font-weight: 500;
    font-size: 30px;
    color: var(--accent-color);
    @include mobile{
      font-size: 24px;
    }
  }

  // .manufacture__stat-text

  &__stat-text {
    font-weight: 500;
    font-size: 16px;
    @include mobile{
      font-size: 14px;
    }
  }
}

.manufacture-info {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .manufacture-info__title

  &__title {
  }

  // .manufacture-info__content

  &__content {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 20px;
    @include small-tablet{
      flex-direction: column-reverse;
      align-items: flex-start;
    }
  }

  // .manufacture-info__text

  &__text {
    max-width: 600px;
    @include mobile{

    }
  }

  // .manufacture-info__patents

  &__patents {

    font-weight: 500;
    display: flex;
    align-items: flex-end;
    gap: 20px;
    line-height: 130%;
    span{
      font-family: var(--font-family-accent);
      font-weight: 700;
      font-size: 90px;
      display: inline;
      line-height: 80%;
    }
  }
}

.manufacture-products {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .manufacture-products__title

  &__title {
    margin: 0;
    margin-bottom: 24px;
  }

  // .manufacture-products__content

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: start;
    gap: 35px;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }

  }
}

.product-card {
  min-width: 100%;
  width: 100%;
  position: relative;
  &--col-2{
    grid-column: 2 span;
    @include small-tablet{
      grid-column: unset;
    }
  }

  @include hover{
    border-radius: 10px 10px 0 0;
    background-color: #15222a;
    padding: 30px 30px 15px 30px;
    .product-card__main-img{
      display: none;
    }

    .product-card__swiper{
      display: block;
      padding-bottom: 15px;
      margin-bottom: 20px;
    }

    .product-card__title{
      color: var(--light-color);
      margin-left: -10px;
    }

    .product-card__info{
      position: absolute;
      top: 99%;
      left: 0;
      right: 0;
      padding: 0 20px 30px 20px;
      display: block;
      background-color: #15222a;
      border-radius: 0 0 10px 10px;
    }
    .product-card__media{
      .product-card__price{
        display: none;
      }
    }
    .product-card__price{
      margin-left: 0;
      margin-bottom: 30px;
      &-new{
        color: #73c6ce;
      }
    }
  }

  &--active{
    border-radius: 10px 10px 0 0;
    background-color: #15222a;
    padding: 30px 30px 15px 30px;
    .product-card__main-img{
      display: none;
    }

    .product-card__swiper{
      display: block;
      padding-bottom: 15px;
      margin-bottom: 20px;
    }

    .product-card__title{
      color: var(--light-color);
      margin-left: -10px;
    }


    .product-card__info{
      position: absolute;
      top: 99%;
      left: 0;
      right: 0;
      padding: 0 20px 30px 20px;
      display: block;
      background-color: #15222a;
      border-radius: 0 0 10px 10px;
    }
    .product-card__media{
      .product-card__price{
        display: none;
      }
    }
    .product-card__price{
      margin-left: 0;
      margin-bottom: 30px;
      &-new{
        color: #73c6ce;
      }
    }
  }

  &__status {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 3;
  }

  // .product-card__main-img

  &__main-img {
    border-radius: 15px;
    height: 100%;
    width: 100%;
    max-height: 280px;
  }

  // .product-card__swiper-wrapper

  &__swiper-wrapper {
  }

  // .product-card__swiper

  &__swiper {
    display: none;

    .product-card__swiper-pagination{
      bottom: 0;
      display: flex;
      .swiper-pagination-bullet{
        flex: 1 1 auto;
        height: 5px;
        border-radius: 0;
        background-color: #d3d3d3;
      }
      .swiper-pagination-bullet-active{
        background-color: var(--accent-color);
      }
    }
  }

  &__swiper-next, &__swiper-prev{
    &::after{
      display: none;
    }
  }

  // .product-card__slide

  &__slide {
  }

  &__slide-img {
    width: 100%;
    max-height: 215px;
    border-radius: 15px;
  }

  // .product-card__slide-content

  &__slide-content {
  }

  // .product-card__swiper-prev

  &__swiper-prev {
  }

  // .product-card__swiper-next

  &__swiper-next {
  }

  // .product-card__title

  &__title {
    font-weight: 700;
    font-size: 19px;
    line-height: 126%;
    color: var(--black-color);
    transition: all .2s ease-in;

    &--500{
      font-weight: 500;
      text-transform: uppercase;
    }
  }

  .catalog-card-title{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 105%;
    color: #fff;
  }
  &__price{
    display: flex;
    align-items: center;
    gap: 20px;
    font-family: var(--font-family-accent);
    margin-top: 15px;
    text-wrap: nowrap;
    @include mobile{
      flex-wrap: wrap;
    }
    &-new{
      font-weight: 700;
      color: var(--accent-color);
    }
    &-old{
      color: #7c7c7c;
      text-decoration: line-through;
      font-weight: 400;
      font-size: 16px;
    }
    .catalog-card-price{
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 20px;
      line-height: 185%;
      color: #73c6ce;
    }
  }

  // .product-card__info

  &__info {
    display: none;
    z-index: 4;
  }

  // .product-card__info-desrk

  &__info-desrk {
    transition: all .2s ease-in;
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    color: rgba(255, 255, 255, 0.8);
    display: block;
    margin-bottom: 60px;
    &--mb-0{
      margin: 0;
    }
  }

  // .product-card__link

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
    color: #060606;
    font-weight: 500;
    background-color: var(--light-color);
    border-radius: 10px;
    gap: 5px;
    @include hover{
      background-color: var(--accent-color);
      color: var(--light-color);
      svg{
        *{
          stroke: var(--light-color)
        }
      }
    }
  }
}

.manufacture-stages {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .manufacture-stages__swiper

  &__swiper {
    position: relative;
  }

  // .manufacture-stages__slide

  &__slide {
    min-height: 640px;
    display: flex;
    align-items: center;
    @include mobile{
      min-height: 400px;
    }
  }

  // .manufacture-stages__slide-content

  &__slide-content {
    padding: 0 130px;
    @include mobile{
      padding: 0 50px;
    }
  }

  // .manufacture-stages__slide-title

  &__slide-title {
    color: var(--light-color);
    margin: 0;
    margin-bottom: 20px;
    @include mobile{
      font-size: 20px;
    }
  }

  // .manufacture-stages__slide-deskr

  &__slide-deskr {
    max-width: 625px;
    font-size: 24px;
    line-height: 130%;
    color: var(--light-color);
    @include mobile{
      font-size: 16px;
    }
  }

  &__swiper-prev, &__swiper-next {
    &::after{
      color: white;
    }
  }

  // .manufacture-stages__swiper-prev

  &__swiper-prev {
    left: 40px;
    @include mobile{
      left: 5px;
    }
    &::after{

    }
  }

  // .manufacture-stages__swiper-next

  &__swiper-next {
    right: 40px;
    @include mobile{
      right: 5px;
    }
  }

  // .manufacture-stages__swiper-pagination

  &__swiper-pagination {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 50px;
    color: #fff;
    font-weight: 700;
    font-size: 30px;
    z-index: 2;
    display: flex;
    .pagination-current{
      color: var(--accent-color);
    }
  }
}

.manufacture-advantages {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .manufacture-advantages__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  // .manufacture-advantages__content

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 35px;

    @include tablet{
      grid-template-columns: 1fr 1fr;
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .manufacture-advantages__card

  &__card {
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 20px;
    border-radius: 15px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__card-title{
    color: var(--light-color);
    margin: 0;
    margin-bottom: 10px;
    line-height: 130%;
  }

  &__card-text{
    color: var(--light-color);
  }
}

.manufacture-note {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .manufacture-note__title

  &__title {
    margin-bottom: 35px;
  }

  // .manufacture-note__content

  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 40px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .manufacture-note__video-wrapper

  &__video-wrapper {
    position: relative;
  }

  // .manufacture-note__video

  &__video {
    width: 100%;
    max-height: 330px;
    border-radius: 15px;
  }

  // .manufacture-note__video-play

  &__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  // .manufacture-note__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
  }

  // .manufacture-note__info-item

  &__info-item {

  }

  // .manufacture-note__info-title

  &__info-title {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 0;
    margin-bottom: 10px;
    &.planner-note-title{
      font-weight: 500;
    }
  }

  // .manufacture-note__info-text

  &__info-text {
    margin: 0;
    line-height: 131%;
  }

  // .manufacture-note__link

  &__link {
    font-weight: 500;
    font-size: 16px;
    padding: 12px 50px;
    background-color: var(--accent-color);
    border-radius: 15px;
    color: var(--light-color);
    font-weight: 500;
  }
}
