.quiz {
  background: linear-gradient(180deg, #06828e 0%, #15222a 100%);
  padding: 100px 0;
  &--mb{
    margin-bottom: 100px;
    @include mobile{
      margin-bottom: 50px;
      padding: 50px 0;
    }
  }
  // .quiz__form

  &__form {
  }

  // .quiz__item

  &__item {
    display: none;
    &--active{
      display: block;
    }
  }

  // .quiz__item-1

  &__item-1-wrapper {
    display: grid;
    grid-template-columns: minmax(200px, 470px) 1fr;
    align-items: flex-start;
    gap: 40px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .quiz__item-1-info

  &__item-1-info {
    display: flex;
    flex-direction: column;
    color: white;
    @include tablet{
      display: none;
    }
  }

  // .quiz__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 20px;
    line-height: 116%;
    font-family: var(--font-family-accent);
  }

  // .quiz__item-subtitle

  &__item-subtitle {
    margin-bottom: 20px;
  }

  // .quiz__item-1-info-img

  &__item-1-info-img {
    margin-bottom: 20px;
  }

  // .quiz__item-1-bonuses

  &__item-1-bonuses {

  }

  &__item-btn-next{
    margin-top: auto;
    margin-left: auto;

    display: flex;
    align-items: center;
    gap: 10px;

    font-weight: 500;
    font-size: 24px;

    @include mobile{
      font-size: 18px;
    }

  }

  // .quiz__item-1-bonuses-title

  &__item-1-bonuses-title {
    font-weight: 500;
    margin-bottom: 20px;
  }

  // .quiz__item-1-bonuses-item

  &__item-1-bonuses-item {
    display: flex;
    align-items: center;
    gap: 10px;
    &:not(:last-child){
      margin-bottom: 8px;
    }
  }

  // .quiz__item-step

  &__item-step {
    padding: 40px;
    border-radius: 15px;
    background-color: var(--light-color);
    min-height: 500px;
    height: 100%;
    display: flex;
    flex-direction: column;

    @include mobile{
      padding: 20px;
    }
  }

  // .quiz__item-step-value

  &__item-step-value {
    display: flex;
    font-weight: 100;
    font-size: 62px;
    line-height: 95%;
    margin-bottom: 45px;

    @include mobile{
      font-size: 46px;
    }
  }

  // .quiz__item-step-corrent

  &__item-step-corrent {
  }

  // .quiz__item-step-value-num

  &__item-step-value-num {

  }

  // .quiz__item-step-total

  &__item-step-total {

  }

  // .quiz__item-step-title

  &__item-step-title {
    margin-bottom: 45px;
    font-family: var(--font-family-accent);
  }

  // .quiz__item-step-inputs

  &__item-step-inputs {
    display: grid;
    align-items: flex-start;
    justify-content: space-between;
    gap: 25px 50px;
    max-width: 1100px;
    margin-bottom: 25px;

    @include mobile{
      gap: 25px 10px;
    }

    &.col-1{
      grid-template-columns: 1fr;
    }
    &.col-2{
      grid-template-columns: repeat(2, 1fr);
    }
    &.col-3{
      grid-template-columns: repeat(3, 1fr);
      @include mobile{
        grid-template-columns: repeat(2, 1fr);
      }
    }
    &.col-4{
      grid-template-columns: repeat(4, 1fr);
      @include small-tablet{
        grid-template-columns: repeat(3, 1fr);
      }
      @include mobile{
        grid-template-columns: repeat(2, 1fr);
      }
    }

    &.quiz__item-step-inputs-imgs{
      grid-template-columns: repeat(3, auto);
      @include tablet{
        grid-template-columns: repeat(2, auto);
      }
    }
  }

  // .quiz__item-step-label

  &__item-step-label {
    display: flex;
    align-items: center;
    gap: 30px;
  }

  // .quiz__item-step-input

  &__item-step-input {
  }

  // .quiz__item-step-input-text

  &__item-step-input-text {
  }

  // .quiz__item-2

  &__item-2 {
    .quiz__item-step-label-other{
      grid-column: 3/4;
      grid-row: 1/4;
      @include tablet{
        grid-area: unset;
      }
    }
  }

  // .quiz__item-step-input-img

  &__item-step-input-img {
    margin-left: auto;
    @include small-tablet{
      display: none;
    }
  }

  // .quiz__item-step-label-other

  &__item-step-label-other {
  }

  // .quiz__item-3

  &__item-3 {
  }

  // .quiz__item-4

  &__item-4 {
  }

  // .quiz__item-final

  &__item-final {

  }

  &__item-final-wrapper{
    display: flex;
    justify-content: space-between;
    gap: 20px;
    background-color: var(--light-color);
    border-radius: 15px;
    overflow: hidden;
  }

  // .quiz__item-final-actions

  &__item-final-actions {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 60px 30px;
  }

  // .quiz__item-final-title

  &__item-final-title {
    margin: 0;
    margin-bottom: 20px;
  }

  // .quiz__item-final-subtitle

  &__item-final-subtitle {
    margin: 0;
    margin-bottom: 25px;
  }

  &__item-final-inputs{
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  // .quiz__item-final-input

  &__item-final-input {
    padding-bottom: 12px;
    border-bottom: 1px solid var(--black-color);
    &:not(:last-child){
      margin-bottom: 20px;
    }
    &:last-child{
      margin-bottom: 40px;
    }
    &::placeholder{
      opacity: 0.3;
    }
  }

  // .quiz__btn-submit

  &__btn-submit {
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: var(--light-color);
    padding: 15px 53px;
    background-color: var(--accent-color);
    border-radius: 10px;

    @include mobile{
      width: 100%;
    }
  }

  // .quiz__item-final-img

  &__item-final-img {
    @include small-tablet{
      display: none;
    }
  }
}

