.catalog-page {

  // .catalog-page__title

  &__title {
  }

  // .catalog-page__list

  &__list {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 30px;
    margin-bottom: 30px;
    @include tablet{
      grid-template-columns: repeat(4, 1fr);
    }
    @include mobile{
      gap: 15px;
    }

  }

  // .catalog-page__item
  .catalog-page-more{
margin: 0 auto;
  }
  &__item {
    display: none;
    &:nth-child(-n+14){
      display: block;
    }
  }

  // .catalog-page__link

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 5px;
    @include hover{
      .catalog-page__link-text{
        color: var(--accent-color);
      }

    }
  }

  // .catalog-page__link-img

  &__link-img {
  }

  // .catalog-page__link-text

  &__link-text {
    text-transform: uppercase;
    color: var(--black-color);
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
            line-clamp: 2;
    -webkit-box-orient: vertical;
    transition: all .2s ease-in;
    @include mobile{
      font-size: 13px;
    }
  }

  // .catalog-page__more-link

  &__more-link {

    color: rgba(31, 30, 30, 0.6);
    display: flex;
    justify-content: center;
    gap: 10px;
    span{
      transform: rotate(180deg);
    }
  }
}

.filter-form {
  padding: 25px 0 50px 0;
  &:has(.filter-form__title input:checked) {
    .filter-form__item{
      display: flex;
    }
  }
  // .filter-form__title

  &__title {
    font-family: var(--font-family-accent);
    font-weight: 500;
    font-size: 20px;
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
    @include mobile{
      padding: 10px 20px;
      background-color: var(--accent-color);
      display: inline-flex;
      border-radius: 5px;
      color: var(--light-color);
    }
      span{
        font-family: var(--font-family);
        font-weight: 400;

        color: #9a9a9a;
        @include mobile{
          color: var(--black-color);
          transform: rotate(180deg);
        }
      }
  }

  // .filter-form__item

  &__item {
    display: flex;
    gap: 22px;
    @include small-tablet{
      flex-direction: column;
    }
    @include mobile{
      display: none;
    }
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  // .filter-form__item-name

  &__item-name {
    min-width: 194px;
    padding-top: 6px;
    font-weight: 400;
    color: #292929;
    flex: none;
      span{
        display: inline;
        font-weight: 600;
        color: var(--black-color);
      }
  }

  // .filter-form__item-actions

  &__item-actions {
    display: flex;
    flex: 1 1 auto;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: space-between;
    @include mobile{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: flex-start;
    }
  }

  // .filter-form__action-radio-label

  &__action-radio-label {
    display: flex;
    align-items: center;
    gap: 7px;
    color: rgba(31, 30, 30, 0.6);
    padding: 6px 16px;
    border: 1px solid var(--accent-color);
    cursor: pointer;
    @include mobile{
      padding: 6px 10px;
    }
    &:hover{
      background-color: var(--accent-color);
      color: var(--light-color);
    }
    &:has(.filter-form__action-radio:checked){
      background-color: var(--accent-color);
      color: var(--light-color);
      .filter-form__action-radio-close{
        display: block;
      }
    }
  }

  // .filter-form__action-radio

  &__action-radio {
  }

  // .filter-form__action-radio-close

  &__action-radio-close {
    display: none;
  }

  // .filter-form__action-radio-text

  &__action-radio-text {
    @include mobile{
      font-size: 13px;
    }
  }

}
.catalog-list {
  margin-bottom: 50px;
  // .catalog-list__content

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 50px;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }
}

.product-proposal {
  grid-column: 2 span;
  display: flex;
  align-items: center;
  gap: 30px;
  border-radius: 10px;
  background: linear-gradient(180deg, #15222a 0%, #9ba7b8 100%);
  padding: 65px 35px;
  min-height: 345px;
  @include tablet{
    flex-direction: column;
  }
  @include mobile{
    grid-column: unset;
  }
  // .product-proposal__img-wrapper

  &__img-wrapper {
    position: relative;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.2);
    width: 100%;
    height: 100%;
    max-height: 177px;
    max-width: 256px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // .product-proposal__img

  &__img {
  }

  &__status{
    position: absolute;
    top: 10px;
    right: 10px;
  }

  // .product-proposal__info

  &__info {
  }

  // .product-proposal__info-title

  &__info-title {
    font-weight: 400;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 20px;
  }

  // .product-proposal__subtitle

  &__subtitle {
    font-weight: 500;
    font-size: 14px;
    color: #ebebeb;
    margin-bottom: 30px;
  }

  // .product-proposal__link

  &__link {
    font-weight: 500;
    font-size: 14px;
    color: var(--black-color);
    padding: 15px;
    background-color: var(--light-color);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    text-align: center;
  }
}
