.enginers-cooperation {
  background: #15222a;
  margin-bottom: 100px;
  padding: 100px 0;
  color: var(--light-color);
  @include mobile{
    padding: 50px 0;
    margin-bottom: 50px;
  }
  // .enginers-cooperation__content

  &__content {
    margin-bottom: 80px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    @include small-tablet{
      grid-template-columns: 1fr;
      gap:40px;
    }
  }

  // .enginers-cooperation__info

  &__info {
  }

  // .enginers-cooperation__title

  &__title {
    margin: 0;
    margin-bottom: 35px;
  }

  // .enginers-cooperation__deskr

  &__deskr {
    margin: 0;
    max-width: 460px;
  }

  // .enginers-cooperation__condition

  &__condition {
    padding: 25px 30px;
    background-color: var(--light-color);
    border-radius: 15px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    color: var(--black-color);
    margin-bottom: 90px;
    @include mobile{
      margin-bottom: 50px;
    }
  }
  &__deskr{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    color: #fff;
  }
  // .enginers-cooperation__condition-title
  &__condition-item-text{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: #000;
  }
  &__condition-title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 25px;
    font-family: var(--font-family);
    line-height: 135%;
    letter-spacing: -0.01em;
    color: #000;

  }

  // .enginers-cooperation__condition-list

  &__condition-list {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__condition-item{
    display: flex;
    align-items: center;
    gap: 10px;

      svg{
       /*  align-self: flex-start; */
        min-width: 23px;
        min-height: 23px;
      }
  }

  // .enginers-cooperation__additionally

  &__additionally {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @include small-tablet{
      gap: 20px;
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .enginers-cooperation__additionally-item

  &__additionally-item {
    padding: 30px 40px;
    display: flex;
    flex-direction: column;
    border-left: 1px solid rgba(255, 255, 255, 0.2);
  }

  // .enginers-cooperation__additionally-title

  &__additionally-title {
    margin-bottom: 15px;
  }

  // .enginers-cooperation__additionally-info

  &__additionally-info {
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
  }

  // .enginers-cooperation__contacts

  &__contacts {
    padding: 20px 40px;
    background-color: var(--light-color);
    border-radius: 15px;
    color: var(--black-color);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  // .enginers-cooperation__contacts-title

  &__contacts-title {
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 8px;
    @include mobile{
line-height: 120%;
    }
  }

  // .enginers-cooperation__contacts-link

  &__contacts-link {
    color: var(--accent-color);
    font-weight: 600;
    font-size: 20px;
  }
}

.useful {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .useful__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  // .useful__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .useful__item

  &__item {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 25px 50px 25px 25px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    &::after{
      content: "";
      display: block;
      position: absolute;
      width: 30px;
      height: 30px;
      bottom: 20px;
      right: 20px;
      border: 1px solid var(--accent-color);
    }
  }

  // .useful__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 25px;
  }

  // .useful__item-deskr

  &__item-deskr {
    margin: 0;
    font-weight: 500;
    line-height: 133%;

  }
}

.consultation {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .consultation__title

  &__title {
    margin: 0;
    margin-bottom: 50px;
  }

  // .consultation__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .consultation__item

  &__item {
    padding: 0 20px 0 40px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    border-left: 1px solid rgba(0, 0, 0, 0.2);;
  }

  // .consultation__item-title

  &__item-title {
    margin: 0;
  }

  // .consultation__item-deskr

  &__item-deskr {
    font-size: 16px;
    margin: 0;
  }
}

.why-me {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .why-me__title

  &__title {
    margin: 0;
    margin-bottom: 30px;
  }

  &__steps{
    gap: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .why-me__item

  &__item {
    border: 0;
    padding: 0;
  }

  // .why-me__item-content

  &__item-content {
    border: 0;
    padding: 0;
    padding: 22px;
    background-color: var(--light-color);
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  // .why-me__item-num

  &__item-num {
    @include fluid-text(62, 24);
    line-height: 95%;
    margin: 0;
    margin-bottom: 5px;
    color: var(--accent-color);
    font-weight: 300;
  }

  // .why-me__item-title

  &__item-title {
    max-width: 260px;
    margin: 0;
    margin-bottom: 15px;
  }

  // .why-me__item-text

  &__item-text {
    margin: 0;
    max-width: 260px;
    font-size: 16px;
  }
}


.docs {
  padding: 100px 0;
  background-color: #ededed;;
  @include mobile{
    padding: 50px 0;
  }
  // .docs__title

  &__title {
    margin: 0;
    margin-bottom: 30px;
  }

  // .docs__content

  &__content {

    display: grid;
    grid-template-columns: 1fr minmax(200px, 450px);
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
    .tabs__content{
      @include tablet{
        order: 2;
      }
    }
  }

  // .docs__panel

  &__panel {
    padding: 30px;
    border-radius: 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    background-color: var(--light-color);
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .docs__panel-item

  &__panel-item {
    display: flex;
    flex-direction: column;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    background-color: var(--light-color);
    padding: 15px;
    border-radius: 15px;
  }

  // .docs__panel-head

  &__panel-head {
    display: flex;
    align-items: center;
    gap: 20px;
    font-weight: 500;
    margin-bottom: 10px;
    line-height: 111%;
    svg{
      min-width: 42px;
      min-height: 42px;
    }
  }

  // .docs__panel-text

  &__panel-text {
    font-size: 14px;
    margin: 0;
    margin-bottom: 5px;
  }

  // .docs__panel-link

  &__panel-link {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 12px;
    color: #7c7c7c;
    align-self: flex-end;
    margin-top: auto;
    @include hover{
      color: var(--accent-color);
      svg{
        *{
          stroke: var(--accent-color);
        }
      }
    }
  }

  // .docs__actions

  &__actions {
    padding: 30px;
    background-color: var(--light-color);
    border-radius: 15px;
    @include tablet{
      order: 1;
    }
  }

  // .docs__actions-title

  &__actions-title {
    margin: 0;
    margin-bottom: 20px;
  }

  .tabs__nav{
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .tabs__nav-btn{
    background-color: #ededed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 16px 15px;
    border-radius: 10px;
    font-weight: 700;
    &:hover{
      background-color: var(--accent-color);
      color: var(--light-color);

      svg{
        path{
          stroke: var(--light-color);
        }
      }
    }
    &--active{
      background-color: var(--accent-color);
      color: var(--light-color);

      svg{
        path{
          stroke: var(--light-color);
        }
      }
    }
  }
}

.eye-on-us {
  padding: 100px 0;
  background: linear-gradient(180deg, #06828e 0%, #15222a 100%);
  @include mobile{
    padding: 50px 0;
  }
  &--mb{
    margin-bottom: 100px;
    @include mobile{
      margin-bottom: 50px;
    }
  }
  // .eye-on-us__wrapper

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    align-items: center;
    color: var(--light-color);
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .eye-on-us__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // .eye-on-us__title

  &__title {
    margin: 0;
    margin-bottom: 32px;
  }

  // .eye-on-us__subtitle

  &__subtitle {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 133%;
    color: #fff;
    margin-bottom: 65px;
  }

  // .eye-on-us__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 25px;
    margin-bottom: 60px;
    max-width: 406px;
  }

  // .eye-on-us__list-item

  &__list-item {
    display: flex;
    align-items: center;
    gap: 10px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: #fff;
  }

  // .eye-on-us__follow

  &__follow {
    display: flex;
    align-items: center;
    gap: 20px;
    background-color: var(--accent-color);
    border-radius: 10px;
    padding: 10px 40px 10px 20px;
    font-weight: 700;
    color: var(--light-color);
  }

  // .eye-on-us__item-wrapper

  &__item-wrapper {
    align-self: flex-end;
    position: relative;
  }

  // .eye-on-us__item-video

  &__item-video {
    border-radius: 20px;
    width: 100%;
    max-width: 100%;
    max-height: 440px;
  }

  // .eye-on-us__video-play

  &__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    svg{
      @include mobile{
        width: 75px;
      }
    }
  }
}

.bim-models {
  padding: 100px 0;
  @include mobile{
    padding: 50px 0;
  }
  // .bim-models__title

  &__title {
    margin: 0;
    margin-bottom: 30px;
  }

  // .bim-models__content

  &__content {
    display: grid;
    grid-template-columns: minmax(200px, 450px) 1fr;
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .bim-models__actions

  &__actions {
    border: 1px solid #ededed;
    border-radius: 15px;
    padding: 30px;
  }

  // .bim-models__actions-title

  &__actions-title {
    margin: 0;
    margin-bottom: 20px;
  }

  // .bim-models__panel

  &__panel {
    border-radius: 15px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);

  }

  &__panel-content{
    padding: 30px;
    display: grid;
    grid-template-columns: minmax(200px, 400px) 1fr;
    align-items: center;
    @include mobile{
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }

  // .bim-models__panel-img

  &__panel-img {
    grid-column: 2 span;
    width: 100%;
    @include mobile{
      grid-column: unset;
    }
  }

  // .bim-models__panel-title

  &__panel-title {
    grid-column: 2 span;
    margin: 0;
    margin-bottom: 5px;
    @include mobile{
      grid-column: unset;
    }
  }

  // .bim-models__panel-text

  &__panel-text {
    font-size: 16px;
    line-height: 130%;
  }

  // .bim-models__panel-link

  &__panel-link {
    color: var(--light-color);
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 17px;
    border-radius: 15px;
    background-color: var(--black-color);
    padding: 27px 38px;
    margin-left: auto;
    justify-content: center;
    @include hover{
      background-color: var(--accent-color);
      svg{
        path{
          fill: var(--light-color);
        }
      }
    }
    @include mobile{
      margin-left: unset;
    }
  }


  .tabs__nav{
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .tabs__nav-btn{
    background-color: #ededed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 16px 15px;
    border-radius: 10px;
    font-weight: 700;
    &:hover{
      background-color: var(--accent-color);
      color: var(--light-color);

      svg{
        path{
          stroke: var(--light-color);
        }
      }
    }
    &--active{
      background-color: var(--accent-color);
      color: var(--light-color);

      svg{
        path{
          stroke: var(--light-color);
        }
      }
    }
  }
}

