    display: grid;
    grid-template-columns: minmax(200px, 350px) 1fr;
    gap: 60px;
    @include tablet{
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  // .last-news__info

  &__info {
    @include tablet{
      display: none;
    }
  }

  &__info-title {
    font-family: var(--font-family-accent);
    color: var(--black-color);
font-weight: 500;
  }

  // .last-news__title

