.footer {
  background-color: #15222a;
  color: var(--light-color);
  padding-bottom: 30px;
  border-radius: 15px 15px 0 0;
  // .footer__head

  &__head {
    padding: 60px 0;
    background: linear-gradient(180deg, #06828e 0%, #15222a 100%);
    @include mobile{
      padding: 60px 0 0 0;
    }
  }

  &__head-wrapper{
    display: grid;
    grid-template-columns: minmax(200px, 520px) minmax(200px, 350px);
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    max-width: 1050px;
    margin: 0 auto;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .footer__head-info

  &__head-info {
  }

  // .footer__head-title

  &__head-title {
    font-family: var(--font-family);
    color: inherit;
    line-height: 145%;
    margin: 0;
    margin-bottom: 15px;
    span{
      color: #67c0c8;
    }
  }

  // .footer__head-advantages

  &__head-advantages {
    display: flex;
    align-items: center;
    gap: 45px;
  }

  // .footer__head-advantage

  &__head-advantage {
    display: flex;
    align-items: center;
    gap: 10px;

    font-size: 14px;
    color: inherit;
  }

  // .footer__head-consultation

  &__head-consultation {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 30px;
    @include mobile{
      margin-top: 0;
    }
  }

  // .footer__head-applicatio-btn

  &__head-applicatio-btn {
    font-weight: 500;
    font-size: 16px;
    line-height: 137%;
    color: var(--black-color);
    text-align: center;
    padding: 14px 90px;
    background-color: var(--light-color);
    border-radius: 15px;
    @include hover{
      background-color: black;
      color: white;
    }
  }

  // .footer__head-consultation-social

  &__head-consultation-social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;

      span{
        font-size: 14px;
        line-height: 157%;
        color: inherit;
      }
  }

  // .footer__head-social-list

  &__head-social-list {
    display: flex;
    gap: 8px;
  }

  // .footer__head-social-item

  &__head-social-item {
  }

  // .footer__head-social-link

  &__head-social-link {
  }

  // .footer__main

  &__main {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @include mobile{
      padding: 60px 0 0 0;
      grid-template-columns: 1fr;
    }
  }

  // .footer__main-info

  &__main-info {
    padding-top: 100px;
    padding-bottom: 50px;
    @include mobile{
      padding-top: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  // .footer__main-title

  &__main-title {
    color: inherit;
    margin: 0;
    margin-bottom: 50px;
  }

  // .footer__main-contacts

  &__main-contacts {
    line-height: 153%;
    color: inherit;
    margin: 0;
    margin-bottom: 10px;
    @include mobile{
      text-align: center;
    }
      a{
        font-weight: 700;
        color: inherit;
        @include hover{
          text-decoration: underline;
        }
        @include mobile{
          padding: 20px 0;
          display: inline-block;
        }
      }
  }

  // .footer__social-list

  &__social-list {
    display: flex;
    align-items: center;
    gap: 40px;
    margin-bottom: 20px;
  }

  // .footer__social-item

  &__social-item {
  }

  // .footer__social-link

  &__social-link {
    @include hover{
      opacity: .8;
    }
  }

  // .footer__main-quality-department

  &__main-quality-department {
    display: flex;
    flex-direction: column;
      a{
        font-weight: 700;
        color: inherit;
        @include hover{
          text-decoration: underline;
        }
      }
  }

  // .footer__bottom

  &__bottom {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    line-height: 186%;
    font-size: 14px;
    gap: 0 20px;
    @include mobile{
      grid-template-columns: 1fr;
      text-align: center;
      gap: 10px;
    }
  }

  // .footer__bottom-mail

  &__bottom-mail {
    color: var(--light-color);
    font-weight: 700;
    @include hover{
      text-decoration: underline;
    }
  }

  // .footer__bottom-polic

  &__bottom-polic {
    text-align: right;
    font-weight: 400;
    color: var(--light-color);
    @include hover{
      text-decoration: underline;
    }
    @include mobile{
      text-align: center;
    }
  }

  // .footer__bottom-company

  &__bottom-company {

  }

  // .footer__bottom-personal

  &__bottom-personal {
    color: var(--light-color);
    text-align: right;
    font-weight: 400;
    @include hover{
      text-decoration: underline;
    }
    @include mobile{
      text-align: center;
    }
  }

		// .footer__reviews-content

		&__reviews-content {
      padding: 25px 0 80px 0;
      background: linear-gradient(180deg, #06828e 0%, #15222a 100%);
      border-radius: 15px 15px 0 0;
      @include mobile{
        padding: 25px 0;
      }
		}

    &__reviews-content-wrapper{

    }

		// .footer__reviews-title

		&__reviews-title {
      margin: 0;
      margin-bottom: 10px;
		}

		// .footer__reviews-subtitle

		&__reviews-subtitle {
      margin-bottom: 35px;
		}

		// .footer__reviews-form

		&__reviews-form {
      display: grid;
      grid-template-columns: 1fr 1fr;
      margin-bottom: 35px;
      gap: 35px;
      @include tablet{
        grid-template-columns: 1fr
      }
      @include mobile{
        margin: 0;
      }
		}

		// .footer__reviews-col-1

		&__reviews-col-1 {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 0px 15px;
      @include mobile{
        grid-template-columns: 1fr;
        gap: 15px;
      }
		}

		// .footer__reviews-label

		&__reviews-label {
      display: flex;
      flex-direction: column;
		}

    &__reviews-label-select{
      grid-column: 2 span;
      @include mobile{
        grid-column: unset;
      }
    }

		// .footer__reviews-label-text

		&__reviews-label-text {
      margin-bottom: 8px;
		}

		// .footer__reviews-input

		&__reviews-input {
      border-radius: 10px;
      padding: 20px;
      display: block;
      width: 100%;
      @include mobile{
        padding: 15px 20px;
      }
		}

    &__reviews-select-wrapper{
      padding: 20px;
      background-color: var(--light-color);
      border-radius: 10px;
      @include mobile{
        padding: 15px 20px;
      }
    }

		// .footer__reviews-select

		&__reviews-select {
      color: #9a9a9a;
      padding: 0;
      border: none;
		}

		// .footer__reviews-label-file

		&__reviews-label-file {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
      padding-bottom: 5px;
      border-bottom: 1px solid white;
      max-width: 190px;
      cursor:pointer;
		}

		// .footer__reviews-file

		&__reviews-file {
		}

		// .footer__reviews-col-2

		&__reviews-col-2 {
		}

		// .footer__reviews-textarea

		&__reviews-textarea {
      height: 170px;
      margin-bottom: 30px;
		}

    &__reviews-submit{
      background-color: var(--accent-color);
      font-weight: 700;
      color: var(--light-color);
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 15px;
      border-radius: 10px;
      padding: 10px;
      font-family: var(--font-family-accent);
    }

		// .footer__reviews-accept

		&__reviews-accept {
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: center;
      gap: 20px;
      @include mobile{
        grid-template-columns: 1fr
      }
		}
}


