.contacts-page {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .contacts-page__title

  &__title {
    margin: 0;
    margin-bottom: 50px;
  }

  // .contacts-page__content

  &__content {
    display: grid;
    grid-template-columns: minmax(200px, 300px) 1fr minmax(200px, 300px);
    gap: 50px;

    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .contacts-page__item

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 25px;

  &-consultation{
    grid-column: 3 span;
    @include tablet{
      grid-column: 2 span;
    }
  }
  }

  // .contacts-page__item-info

  &__item-info {
    display: flex;
    flex-direction: column;

    gap: 15px;
    height: 100%;
  }

  // .contacts-page__item-title

  &__item-title {
    font-weight: 600;
  }

  // .contacts-page__item-text

  &__item-text {
    display: flex;
    align-items: center;
    color: var(--black-color);
    gap: 10px;
  }

  &__item-link{
    @include hover{
      text-decoration: underline;
    }
  }

  // .contacts-page__item-img

  &__item-img {
    width: 100%;
    @include tablet{
      display: none;
    }
  }
}

.contacts-location {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .contacts-location__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  // .contacts-location__content

  &__content {
    display: grid;
    grid-template-columns: minmax(100px, 266px) minmax(100px, 500px);
    justify-content: center;
    gap: 80px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .contacts-location__video-wrapper

  &__video-wrapper {
  }

  // .contacts-location__video

  &__video {
    border-radius: 15px;
    @include tablet{
      min-height: 400px;
    }
    @include mobile{
      min-height: 500px;
    }
  }

  // .contacts-location__info

  &__info {
    display: flex;
    flex-direction: column;
    gap: 50px;
  }

  // .contacts-location__item

  &__item {
    display: flex;
    flex-direction: column;

  }

  // .contacts-location__item-title

  &__item-title {
    margin: 0;
    font-weight: 600;
    line-height: 130%;
    margin-bottom: 10px;
  }

  // .contacts-location__item-text

  &__item-text {
    margin: 0;
    line-height: 153%;
    margin-bottom: 25px;
  }

  // .contacts-location__item-navigations

  &__item-navigations {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 10px;
    @include small-tablet{
      grid-template-columns: 1fr 1fr;
    }
  }

  // .contacts-location__item-navigation

  &__item-navigation {
    border: 1px solid #06828e;
    border-radius: 13px;
    display: flex;
    align-items: center;
    min-height: 90px;
    padding: 10px;
    gap: 15px;
    font-weight: 600;
    font-size: 14px;
    color: var(--black-color);
    @include mobile{
      flex-direction: column;
      justify-content: space-between;
    }
    @include hover{
      background-color: var(--accent-color);
      color: var(--light-color);
      svg{
        *{
          fill: var(--light-color)
        }
      }
    }

  }

  // .contacts-location__item-navigation-img

  &__item-navigation-img {
  }

  // .contacts-location__item-navigation-text

  &__item-navigation-text {
  }
}

.contacts-partners {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .contacts-partners__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }
}

.contacts-additionally {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .contacts-additionally__content

  &__content {
    display: grid;
    grid-template-columns: minmax(200px, 490px) 1fr;
    gap: 30px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .contacts-additionally__requisites

  &__requisites {
    display: flex;
    flex-direction: column;
  }

  // .contacts-additionally__requisites-title

  &__requisites-title {
    margin: 0;
    margin-bottom: 30px;
  }

  // .contacts-additionally__requisites-content

  &__requisites-content {
    padding: 30px;
    color: #2e2e2e;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    background-color: var(--light-color);
    border-radius: 15px;
    height: 100%;
  }

  // .contacts-additionally__requisites-text

  &__requisites-text {
    line-height: 153%;
    color: #2e2e2e;
    margin: 0;
  }

  // .contacts-additionally__social

  &__social {
  }

  // .contacts-additionally__social-title

  &__social-title {
    margin: 0;
    margin-bottom: 30px;
  }

  // .contacts-additionally__social-list

  &__social-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 13px;
    @include mobile{
      grid-template-columns: 1fr 1fr;
    }
  }

  // .contacts-additionally__social-item

  &__social-item {

  }

  // .contacts-additionally__social-link

  &__social-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    height: 100%;
    width: 100%;

    @include hover{
      box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
    }
  }

  // .contacts-additionally__social-text

  &__social-text {
    font-size: 16px;
    color: #2e2e2e;
    line-height: 162%;
  }

  // .contacts-additionally__social-img

  &__social-img {
  }
}
