.header{
  padding: 15px 0;
  position: relative;
  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__contacts{
    display: flex;
    align-items: center;
    gap: 14px;
  }
  &__contacts-list{
    display: flex;
    flex-direction: column;
    text-wrap: nowrap;
  }
  &__contacts-link{
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    text-transform: uppercase;
    color: var(--accent-color);

    @include hover{
      color: black;
    }
  }
  &__callme{
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    color: var(--light-color);
    text-align: center;
    background-color: var(--accent-color);
    padding: 11px 21px;
    border-radius: 100px;

    @include hover{
      background-color: black;
    }
  }

  &__cart-link{
    @include tablet{
      margin-left: auto;
    }
    @include hover{
      *{
        stroke: black;
      }
    }

  }

}

.logo {
  position: relative;
  z-index: 7;
}

.menu{
  margin-left: auto;
  @include tablet{
  position: fixed;
  background-color: white;
  bottom: 0;
  top: 0;
  right: -100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
  align-items: center;
  transition: right .2s ease-in;
  z-index: 12;
  &--active{
    right: 0;
  }
  }
  &__item {
    padding: 10px 0;
    &-dropdown{
      @include hover{
        .menu__dropdown{
          visibility: visible;
          opacity: 1;
        }
      }
      @include tablet{
        display: none;
      }
    }
  }
  &__list {
    display: flex;
    column-gap: 30px;
    @include tablet{
      flex-direction: column;
      row-gap: 20px;
      text-align: center;
      margin: 0 auto
       }
  }
  &__link{
    font-weight: 400;
    font-size: 14px;
    line-height: 157%;
    text-transform: uppercase;
    color: #000;

    @include hover{
      color: var(--accent-color)
    }
  }

  // .menu__dropdown

  &__dropdown {
    position: absolute;
    z-index: 2;
    top: 77px;
    background-color: #f2f2f2;
    left: 0;
    width: 100%;
    padding: 40px 0;
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease-in;
    @include tablet{
      display: none;
    }
  }

  &__dropdown-content{
    display: grid;
    grid-template-columns: minmax(200px, 256px) minmax(200px, 256px) 1fr;
    gap: 40px clamp(3.125rem, -15.057rem + 28.41vw, 6.25rem);
  }

  // .menu__dropdown-link

  &__dropdown-link {
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: #191919;
    @include hover{
      .menu__dropdown-link-head{
        color: var(--accent-color);
        svg{
          path{
            stroke: var(--accent-color)
          }
        }
      }
    }
  }

  // .menu__dropdown-link-head

  &__dropdown-link-head {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;
    transition: all .2s ease-in;
    svg{
      width: 24px;
      height: 24px;
    }
  }

  // .menu__dropdown-link-deskr

  &__dropdown-link-deskr {
    font-weight: 400;
    font-size: 14px;
    color: #1d1d1d;
    margin: 0;
    margin-left: 34px;
  }

  // .menu__dropdown-form

  &__dropdown-form {
    grid-column: 3/4;
    grid-row: 1/4;
    border-radius: 10px;
    background-color: var(--light-color);
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }

  // .menu__dropdown-form-input

  &__dropdown-form-input {
    padding-bottom: 13px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
    &::placeholder{
      opacity: 0.3;
    }
  }

  // .menu__dropdown-form-submit

  &__dropdown-form-submit {
  }

}

.burger{
  @include burger;
  display: none;
  @include tablet{
  display: block;
    z-index: 13;
 }
}
