.article {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .article__wrapper

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 290px);
    gap: 20px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .article__content

  &__content {
    min-width: 100%;
    @include tablet{
      order: 2;
    }

    h4{
      margin-bottom: 5px;
    }
    img{
      display: block;
    }
    span{
      &:not([class]){
        display: inline;
      }

    }
    ul{
      display: flex;
      flex-direction: column;
      gap: 20px;
      margin-bottom: 60px;
    }
    li{
      span{
        text-decoration: underline;
      }
    }
    table{
      border-radius: 15px;
      box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
      border-collapse: collapse; /*    */
      border: 0;
      margin-bottom: 60px;
      min-width: 100%;
      font-size: 16px;
      @include mobile{
        font-size: 11px;
      }
    }

    tr{
      &:not(:last-child){
        border-bottom: 1px solid #ddd; /*      */
      }
    }
    td{
      &:not(:last-child){
        border-right: 1px solid #ddd; /*      */
      }
    }
    th{
      border-bottom: 1px solid #ddd; /*      */
      font-weight: 400;
      color: var(--accent-color);
      &:not(:last-child){
        border-right: 1px solid #ddd; /*      */
      }
    }
    th, td{
      text-align: center;
      padding: 20px;
      @include mobile{
        padding: 12px 8px;
      }
    }
  }

  // .article__main-img

  &__main-img {
    position: relative;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: hidden;
    margin-bottom: 40px;
  }

  // .article__main-img-content

  &__main-img-content {
    backdrop-filter: blur(23.299999237060547px);
    background: rgba(0, 0, 0, 0.4);
    border-radius: 20px;
    padding: 25px;
    color: var(--light-color);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  // .article__main-info

  &__main-info {
    display: flex;
    align-items: center;
    gap: 25px;
    margin-bottom: 15px;
  }

  // .article__main-date

  &__main-date {
    font-size: 16px;
  }

  // .article__main-views

  &__main-views {
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 5px;
  }

  // .article__main-title

  &__main-title {
    max-width: 470px;
    margin: 0;
    line-height: 116%;
  }

  // .article__read-time

  &__read-time {
    display: inline-flex;
    align-items: center;
    gap: 5px;
    background: #ededed;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 20px;
  }

  // .article__text

  &__text {

  }

  // .article__img

  &__img {
    width: 100%;
  }

  // .article__quote

  &__quote {
    background: #ededed;
    display: flex;
    align-items: flex-start;
    gap: 40px;
    padding: 40px;
    margin-bottom: 60px;
    @include mobile{
      margin-bottom: 30px;
      padding: 25px;
      flex-direction: column;
    }
  }

  // .article__quote-img

  &__quote-img {
    flex: none;
  }

  // .article__quote-content

  &__quote-content {

  }

  // .article__quote-author

  &__quote-author {
    color: var(--black-color);
  }

  // .article__quote-text

  &__quote-text {
    font-weight: 600;
    color: var(--black-color);
  }

  // .article__products

  &__products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-bottom: 80px;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .article__product

  &__product {
    min-height: 240px;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 18px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  // .article__product-img

  &__product-img {
  }

  // .article__product-info

  &__product-info {
    backdrop-filter: blur(4px);
    background: rgba(255, 255, 255, 0.8);
    border-radius: 18px;
    padding: 15px 20px;
  }

  // .article__product-title

  &__product-title {
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 15px;
  }

  // .article__product-link

  &__product-link {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 500;
    color: var(--black-color);
    @include hover{
      color: var(--accent-color);
    }
  }

  // .article__aside

  &__aside {
    padding: 25px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    @include tablet{
      order: 1;
    }
  }

  &__aside-content{
    position: sticky;
    top: 25px;
  }

  // .article__aside-title

  &__aside-title {
    font-size: 18px;
    font-weight: 600;
  }

  // .article__aside-list

  &__aside-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-left: 25px;
    a{
      @include hover{
        color: var(--accent-color);
      }
    }
  }

  // .article__aside-item

  &__aside-item {
    a{
      color: #2e2e2e;
      font-size: 14px;
    }
    ul{
      display: flex;
      flex-direction: column;
      gap: 10px;
      margin-top: 15px;
      list-style: none;
      padding: 0;
      li{
        position: relative;
        padding-left: 12px;
        &::after{
          display: block;
          content: "";
          position: absolute;
          top: 10px;
          left: 0;
          width: 2px;
          height: 2px;
          background-color: var(--black-color);
        }
      }
    }
  }

  // .article__aside-link

  &__aside-link {
    text-decoration: underline;
  }

  &__video-wrapper{
    width: 100%;
    position: relative;
  }

  &__video-play{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__video{
    width: 100%;
    max-height: 500px;
  }

  &__table-wrapper{
    overflow: auto;
    padding: 15px;
    margin: -15px;
    &::-webkit-scrollbar{
      display: none;
    }
  }
}

.articles-other {
  // .articles-other__head

  .swiper-button-next, .swiper-button-prev{
    position: static;
    inset: none;
    margin: 0;
    min-height: 30px;
    min-width: 30px;
    width: unset;
    height: unset;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    border-radius: 100%;
    @include hover{
      svg{
        path{
          stroke: var(--accent-color)
        }
      }
    }
    &::after{
      display: none;
    }
  }

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }

  // .articles-other__title

  &__title {
  }

  // .articles-other__btns

  &__btns {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  // .articles-other__swiper

  &__swiper {
    padding: 10px;
    margin: -10px;
  }

  // .articles-other__item

  &__item {
    display: flex;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    color: var(--black-color);
    &:hover{
      .articles-other__item-title{
        color: var(--accent-color);
      }
    }
  }

  // .articles-other__item-img

  &__item-img {
  }

  // .articles-other__item-info

  &__item-info {
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include small-tablet{
      width: 100%;
    }
  }

  // .articles-other__item-date

  &__item-date {
    font-weight: 600;
    font-size: 14px;
    margin-bottom: 20px;
  }

  // .articles-other__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 16px;
    transition: all .2s ease-in;
  }

  // .articles-other__item-read-time

  &__item-read-time {
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: 400;
    font-size: 14px;
    padding: 8px;
    background: #ededed;
    border-radius: 10px;
    align-self: flex-end;
  }
}

[data-video-play]{
  @include mobile{
    svg{
      width: 50%;
    }
  }
}
