html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: var(--black-color);
  font-weight: 400;
  line-height: 124%;

  @include fluid-text(17, 16)
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-mobile{
  @include mobile{
    display: none !important;
  }
}

.hidden-tablet{
  @include tablet{
    display: none !important;
  }
}

.visible-tablet{
  @include for-tablet{
    display: none !important;
  }
}

.visible-mobile{
  @include for-mobile{
    display: none !important;
  }
}

h1, .h1{
  @include fluid-text(40, 32);
  line-height: 150%;
  font-weight: 700;
  font-family: var(--font-family-accent)
}

h2, .h2{
  @include fluid-text(32, 24);
  line-height: 150%;
  font-weight: 700;
  font-family: var(--font-family-accent)
}

h3, .h3{
  @include fluid-text(24, 20);
  line-height: 150%;
  font-weight: 700;
  font-family: var(--font-family-accent)
}

h4, .h4{
  @include fluid-text(19, 16);
  line-height: 150%;
  font-weight: 700;
  font-family: var(--font-family-accent)
}

a,
button,
label,
input,
textarea,
select,
svg * {
  transition: all var(--transition-duration) ease-in;
}

span{
  display: block;
}

input, select{
  outline: transparent;
}

.list{
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
  &__item{
    padding-left: 22px;
    position: relative;
    font-weight: 600;

    &::after{
      display: block;
      content: "";
      width: 8px;
      height: 8px;
      border-radius: 100%;
      background-color: var(--accent-color);
      position: absolute;
      top: 5px;
      left: 0;
    }
  }
}

.tabs{
  @include tabs
}

.btn-1{
  background-color: var(--accent-color);
  color: var(--light-color);
  padding: 15px 25px;
  border-radius: 10px;
  font-weight: 700;
  transition: all .2s ease-in;
  @include hover{
    background-color: var(--black-color);
  }
}

.btn-2{
  background-color: var(--black-color);
  color: var(--light-color);
  padding: 15px 25px;
  border-radius: 50px;
  font-weight: 700;
  transition: all .2s ease-in;
  display: inline-block;
  @include hover{
    background-color: var(--accent-color);
    color: var(--light-color);
  }
}

.ymaps-layers-pane {
	filter: grayscale(1);
	-ms-filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-o-filter: grayscale(1);
}

.custom-balloon{
  text-align: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 117%;
  text-align: center;
  color: #fff;
  background-color: var(--accent-color);
  display: flex;
  flex-direction: column;
  gap: 12px;
  height: 100%;
  width: 100%;
  padding: 15px;
  border-radius: 10px;
    strong{
      font-weight: 500;
      font-size: 14px;
      line-height: 132%;
      text-align: center;
      color: #fff;
    }
}

.ymaps-map{
  width: 100% !important;
}

.ymaps-b-balloon__content{
  background-color: transparent !important;
}

.ymaps-b-balloon__content-holster{
  padding: 0 !important;
}

.ymaps-b-balloon__tr{
  display: none !important;
}

.ymaps-b-balloon__tl{
  display: none !important;
}

.ymaps-b-balloon__bl{
  display: none !important;
}

.ymaps-b-balloon__br{
  display: none !important;
}

.ymaps-shadows-pane{
  display: none !important;
}

.ya-map-style{
  @include mobile{
    height: 400px !important;
  }
}

.hero-1 {

  &__content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    min-height: 520px;

    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .dealers__info

  &__info {
    padding-bottom: 20px;
  }

  // .dealers__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
    line-height: 118%;
    span{
      background-size: 100% 100%;
      display: inline-block;
      padding: 20px 20px 28px 20px;
      margin: -20px -20px -28px -20px;
      color: var(--light-color);
      @include tablet{
        margin: -20px 0px -28px 0px;
      }
    }
  }

  // .dealers__subtitle

  &__subtitle {
    margin-bottom: 20px;
  }

  // .dealers__application

  &__application {
    font-weight: 400;
  }

  &__actions{
   position: relative;
   display: flex;
   flex-direction: column;
   @include tablet{
    margin: 0 -15px;
   }
  }

  &__actions-links{
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    padding: 18px 25px;
    backdrop-filter: blur(4px);
    background: rgba(255, 255, 255, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    gap: 10px;
    @include tablet{
      position: static;
      background-color: #15222a;

    }
  }

  &__actions-link{
    font-size: 14px;
    padding: 10px 20px;
    border-radius: 10px;
    background-color: var(--light-color);
    color: var(--black-color);
    @include hover{
      background-color: var(--accent-color);
      color: var(--light-color);
    }
  }

  &__actions-img{

    width: 100%;
    display: block;
    margin-top: auto;
    @include tablet{
      display: none;
    }
  }

  .breadcrumbs{
    margin-top: 65px;
    @include mobile{
      margin-top: 30px;
    }
  }
}

