.blog-hero {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .blog-hero__content

  &__content {
    display: grid;
    grid-template-columns: minmax(200px, 390px) 1fr;
    gap: 25px;
    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .blog-hero__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // .blog-hero__date

  &__date {
    font-weight: 600;
    margin-bottom: 25px;
  }

  // .blog-hero__title

  &__title {
    margin: 0;
    margin-bottom: 15px;
  }

  // .blog-hero__text

  &__text {
  }

  // .blog-hero__lastnews

  &__lastnews {
  }

  // .blog-hero__lastnews-head

  &__lastnews-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
    padding: 0 15px 0 35px;
    flex-wrap: wrap;
    @include tablet{
      padding: 0;
    }
  }

  // .blog-hero__lastnews-title

  &__lastnews-title {
  }

  // .blog-hero__allnews-link

  &__allnews-link {
    color: var(--black-color);
    transition: all .2s ease-in;
    @include hover{
      text-decoration: underline;
    }
  }

  // .blog-hero__lastnews-wrapper

  &__lastnews-wrapper {
    padding: 35px;
    border-radius: 15px;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);

    display: flex;
    gap: 30px;

    @include small-tablet{
      flex-direction: column;
      padding: 20px;
    }
  }

  // .blog-hero__lastnews-img

  &__lastnews-img {
    border-radius: 15px;
  }

  // .blog-hero__lastnews-info

  &__lastnews-info {
    display: flex;
    flex-direction: column;
  }

  // .blog-hero__lastnews-date

  &__lastnews-date {
    font-weight: 600;
    margin-bottom: 15px;
  }

  &__lastnews-info-title {
    margin: 0;
    margin-bottom: 10px;
  }

  // .blog-hero__lastnews-text

  &__lastnews-text {
    margin: 0;
    margin-bottom: 20px;
  }

  // .blog-hero__lastnews-link

  &__lastnews-link {
    font-family: var(--font-family-accent);
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 500;
    color: var(--black-color);
    margin-top: auto;
    @include hover{
      color: var(--accent-color);
    }
  }
}

.news {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .news__actions

  &__actions {
    display: flex;
    align-items: center;
    gap: 30px 60px;
    margin-bottom: 35px;
    flex-wrap: wrap;
    @include small-tablet{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  // .news__action-select-label

  &__action-select-label {
    background-color: var(--accent-color);
    display: block;
    padding: 15px;
    border-radius: 10px;
    width: 100%;
    max-width: 262px;
  }

  // .news__action-select

  &__action-select {
    font-weight: 700;
    font-size: 17px;
    line-height: 218%;
    background-color: transparent;
    color: var(--light-color);
    width: 100%;

  }

  // .news__action-radios

  &__action-radios {
    display: flex;
    gap: 20px;
    @include small-tablet{
      flex-wrap: wrap;
    }
  }

  // .news__action-radio-label

  &__action-radio-label {
    display: flex;
    align-items: center;
    gap: 7px;
    color: rgba(31, 30, 30, 0.6);
    padding: 6px 16px;
    border: 1px solid var(--accent-color);
    cursor: pointer;
    &:hover{
      background-color: var(--accent-color);
      color: var(--light-color);
    }
    &:has(.news__action-radio:checked){
      background-color: var(--accent-color);
      color: var(--light-color);
      .news__action-radio-close{
        display: block;
      }
    }
  }

  // .news__action-radio

  &__action-radio {
  }

  // .news__action-radio-close

  &__action-radio-close {
    display: none;
  }

  // .news__action-radio-text

  &__action-radio-text {
  }

  // .news__content

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 60px 45px;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .news__item

  &__item {
    display: flex;
    flex-direction: column;
  }

  // .news__item-img

  &__item-img {
    margin-bottom: 20px;
    border-radius: 18px;
  }

  // .news__item-date

  &__item-date {
    font-weight: 600;
    margin-bottom: 10px;
  }

  // .news__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 15px;
    color: var(--black-color);
    @include hover{
      color: var(--accent-color);
    }
  }

  // .news__item-text

  &__item-text {
    margin: 0;
    margin-bottom: 25px;
  }

  // .news__item-link

  &__item-link {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 500;
    color: var(--black-color);

    @include hover{
      color: var(--accent-color);
      *{
        stroke: var(--accent-color)
      }
    }
  }

  // .news__form

  &__form {
    grid-column: 2 span;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: 2px 2px 17px 0 rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    @include mobile{
      grid-column: unset;
      padding: 20px;
    }
  }

  // .news__form-title

  &__form-title {
    margin: 0;
    margin-bottom: 15px;

  }

  // .news__form-subtitle

  &__form-subtitle {
    margin-bottom: 30px;
  }

  // .news__form-input

  &__form-input {
    padding: 12px 15px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    width: 100%;
    &:not(:last-child){
      margin-bottom: 20px;
    }
    &:last-child{
      margin-bottom: 50px;
    }
    &::placeholder{
      color: rgba(0, 0, 0, 0.6);
    }
  }

  // .news__form-bottom

  &__form-bottom {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    @include mobile{
      display: flex;
      flex-direction: column-reverse;
    }
  }

  // .news__form-submit

  &__form-submit {
    font-weight: 700;
    color: var(--light-color);
    background-color: var(--accent-color);
    border-radius: 10px;
    padding: 15px;
  }
}

.single-news {
  margin-bottom: 100px;
  padding: 100px 0;
  background-color: #15222a;;
  @include mobile{
    margin-bottom: 50px;
    padding: 50px 0;
  }
  // .single-news__wrapper

  &__wrapper {
  }

  // .single-news__head

  &__head {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 35px;
    flex-wrap: wrap;
  }

  // .single-news__title

  &__title {
    max-width: 620px;
    color: var(--light-color);
    margin: 0;
  }


  // .single-news__date

  &__date {
    font-weight: 600;
    color: var(--light-color);
  }

  // .single-news__content

  &__content {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 340px);
    gap: 40px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .single-news__img

  &__img {
    border-radius: 30px;
    width: 100%;
  }

  // .single-news__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // .single-news__text

  &__text {
    color: var(--light-color);
    margin: 0;
    margin-bottom: 55px;
    line-height: 131%;
  }

  // .single-news__link

  &__link {
    display: flex;
    align-items: center;
    gap: 15px;
    color: var(--light-color);
    @include hover{
      color: var(--accent-color);
      svg{
        *{
          stroke: var(--accent-color)
        }
      }
    }
  }
}
.academy {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  &__title{
    margin: 0;
    margin-bottom: 30px;
  }

  // .academy__content

  &__content {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 482px);
    gap: 50px;
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  // .academy__img

  &__img {
    width: 100%;
  }

  // .academy__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  // .academy__info-title

  &__info-title {
    display: flex;
    align-items: flex-start;
    margin: 0;
    margin-bottom: 20px;
    gap: 12px;
    line-height: 110%;
  }

  &__info-subtitle{
      font-weight: 600;
      font-size: 20px;
      color: var(--accent-color);
      margin-bottom: 10px;
    span{
      display: inline;
      padding-left: 10px;
      font-weight: 400;
      font-size: 16px;
      color: var(--black-color);
    }
  }

  // .academy__text

  &__text {
    margin: 0;
    margin-bottom: 50px;
  }

  // .academy__link

  &__link {
    font-weight: 700;
    color: var(--light-color);
    padding: 15px 30px;
    border-radius: 10px;
    background-color: var(--accent-color);
    margin-top: auto;
  }
}

.academy-blog{
  &__text{
    padding-left: 0;
  }
}
