.faq-page {

  // .faq-page__head

  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 30px;
    @include small-tablet{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  // .faq-page__title

  &__title {
    margin: 0;
  }

  // .faq-page__search

  &__search {
    width: 100%;
    max-width: 360px;
  }

  // .faq-page__search-label

  &__search-label {
    padding: 16px 20px;
    display: flex;
    align-items: center;
    gap: 25px;
    background: #f8f8f8;
    border-radius: 28px;
  }

  // .faq-page__search-input

  &__search-input {
    background-color: transparent;
  }
}



.montage-methods {
  margin-bottom: 40px;
  // .montage-methods__title

  &__title {
    margin: 0;
    margin-bottom: 35px;
  }

  // .montage-methods__content

  &__content {
    display: grid;
    grid-template-columns: minmax(200px, 360px) 1fr;
    gap: 50px 80px;
    margin-bottom: 20px;

    @include tablet{
      grid-template-columns: 1fr;
    }
  }

  // .montage-methods__video-wrapper

  &__video-wrapper {
    position: relative;
    @include tablet{
      display: none;
    }
  }

  // .montage-methods__video

  &__video {
    border-radius: 10px;
    width: 100%;
    height: 100%;
  }

  // .montage-methods__video-play

  &__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  // .montage-methods__img

  &__img {

  }

  &__imgs{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
  }
}
