
.breadcrumbs {
    margin: 20px 0 40px 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
		// .breadcrumbs__item
  @include mobile{
    margin:5px  0 20px 0;
  }
		&__item {
      &:not(:first-child){
        padding-left: 18px;
        position: relative;
        &::after{
          display: block;
          content: "";
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 8px;
          height: 1px;
          background-color: #06828e;
        }
      }
		}

		// .breadcrumbs__item-link

		&__item-link {
      font-weight: 500;
      font-size: 13px;
      line-height: 129%;
      color: #6b7170;
      @include hover{
        color: var(--accent-color);
      }
		}

		// .breadcrumbs__item-link--active

		&__item-link--active {
      color: var(--black-color);
		}
}

