.page-reviews {
  margin-bottom: 70px;
  @include mobile{
    margin-bottom: 50px;
  }
		// .page-reviews__title

		&__title {
      margin: 0;
      margin-bottom: 35px;
		}

		// .page-reviews__swiper

		&__swiper {
      margin-bottom: 40px;
		}

		// .page-reviews__swiper-imgs

		&__swiper-imgs {
      margin-bottom: 25px;
		}

    &__swiper-imgs-prev, &__swiper-imgs-next {
      color: white;

      &::after{
        @include small-tablet{
          font-size: 25px;
        }
      }

		}

    &__swiper-imgs-prev {
      left: 0;
    }

		&__swiper-imgs-next {
      right: 0;
		}

    &__swiper-img{
      display: block;
      width: 100%;
    }

		// .page-reviews__wrapper-content

		&__wrapper-content{
      display: grid;
      grid-template-columns: minmax(200px, 250px) minmax(200px, 700px);
      justify-content: space-between;
      gap: 20px;
      box-shadow: 2px 2px 19px 0 rgba(55, 55, 55, 0.15);

      padding: 30px 60px 30px 30px;
      border-radius: 15px;

      @include small-tablet{
        grid-template-columns: 1fr;
      }
      @include mobile{
        padding: 15px
      }
		}

		// .page-reviews__info

		&__info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;
      @include small-tablet{
        flex-direction: row;
        align-items: center;
        gap: 30px;
      }
		}

		// .page-reviews__info-avatar

		&__info-avatar {
		}

		// .page-reviews__info-name

		&__info-name {
      font-weight: 500;
		}

		// .page-reviews__info-additionally

		&__info-additionally {
      font-weight: 500;
      color: #686868;
		}

		// .page-reviews__content

		&__content {
      display: flex;
      flex-direction: column;
		}

		// .page-reviews__content-text

		&__content-text {
      display: -webkit-box;
      -webkit-line-clamp: 5; /* number of lines to show */
              line-clamp: 5;
      -webkit-box-orient: vertical;
      overflow: hidden;
		}

		// .page-reviews__content-title

		&__content-title {
      display: flex;
      font-weight: 600;
      font-size: 25px;
      line-height: 125%;
      gap: 15px;
		}

		// .page-reviews__content-more

		&__content-more {
      margin-left: auto;
      color: var(--accent-color);
      margin-bottom: 30px;
		}

		// .page-reviews__navigation

		&__navigation {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;

      .page-reviews__navigation-pagination{
        width: auto;

      }
		}

		// .page-reviews__navigation-prev

		&__navigation-prev, &__navigation-next {
      position: static;
      display: flex;
      align-items: center;
      gap: 10px;
      font-size: 14px;
      padding: 11px 18px;
      background-color: var(--accent-color);
      border-radius: 10px;
      width: unset;
      height: unset;
      color: white;
      margin: 0;
      transition: all .2s ease-in;
      @include hover{
        background-color: var(--black-color);
      }
      &::after{
        display: none;
      }
		}

		// .page-reviews__navigation-pagination

		&__navigation-pagination {
      position: static;
      @include mobile{
        display: none;
      }
      .swiper-pagination-bullet{
        width: 15px;
        height: 15px;
      }
      .swiper-pagination-bullet-active{
        background-color: var(--accent-color);
      }

		}

		// .page-reviews__navigation-next

		&__navigation-next {
      position: static;
      display: flex;
      align-items: center;
      gap: 10px;
      &::after{
        display: none;
      }
		}
}

