.video-reviews {
  margin-bottom: 100px;
  @include mobile{
    margin-bottom: 50px;
  }
  // .video-reviews__title

  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  // .video-reviews__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
    }
  }

  // .video-reviews__item

  &__item {
  }

  // .video-reviews__item-wrapper

  &__item-wrapper {
    position: relative;
    margin-bottom: 10px;
  }

  // .video-reviews__item-video

  &__item-video {
    border-radius: 10px;
    display: block;
    width: 100%;
    max-height: 263px;
    @include mobile{
      max-height: unset;
    }
  }

  // .video-reviews__video-play

  &__video-play {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  // .video-reviews__item-text

  &__item-text {

  }
}
